@import 'scss_header';

// s : Flex Box
.flex-box {
	$columnCnt: 7; // 컬럼 갯수(2~7)
	@include justify_width(start);

	.flex-box-contents {
		flex: 1;
	}

	@for $i from 1 through $columnCnt {
		&.col-#{$i} {
			flex-wrap: wrap;

			.flex-box-contents {
				-ms-flex: 0 1 percentage(1 / $i);
				flex: 0 1 percentage(1 / $i);
				width: percentage(1 / $i);
			}
		}
	}
}

// e  : Flex Box
.prod-detail-tab-wrap  {
	.editor-wrap {
		p:not(.star-text),div, span {
			margin: 0;
			padding: 0;
			border: 0;
			font-family: $fontMix !important;
		}

		img {
			max-width: 100% !important;
			width: 100% !important;
			height: auto !important;
			margin: 0;
			padding: 0;
			border: 0;
		}

		.product-detail-wrap {
			position: relative;

			p,div, span {
				margin: 0;
				padding: 0;
				border: 0;
				font-family: $fontMix !important;
			}
	
			img {
				max-width: 100% !important;
				width: 100% !important;
				height: auto !important;
				margin: 0;
				padding: 0;
				border: 0;
			}
			
			.image {
				position: relative;

				&.col-2 {
					@include justify_width(start);
					
					img {
						flex: 0 1 50%;
						width: 50% !important;
						height: unset !important;
						max-height: 50vw !important;
						object-fit: cover !important;
					}
				}

				&.line-top {
					border-top: 1px solid #000;
				}
		
				&.line-bottom {
					border-bottom: 1px solid #000;
				}

				.line-center {	
					border-right: 1px solid #000;
					font-size: 0;
				}
			}

			.text {
				padding: 30px 30px 120px 30px;
				font-size: 1.6rem;
				line-height: 2.8rem;

				strong {
					font-size: 2.4rem;
				}
			}

			.product-title {
				position: absolute;
				left: 0;
				right: 0;
				bottom: 20vw;
				font-size: 12vw;
				white-space: nowrap;
				color: #fff;
				text-align: center;
				letter-spacing: normal;
			}
		}
	}
}

//s : 리뷰 박스
.review-content-wrap {
	text-align: left;
	@include justify_width(start);
	overflow: hidden;

	.review-info-wrap {
		flex: 1;
		overflow: hidden;

		.rating-list {
			font-size: 0;
			line-height: 1;

			li {
				display: inline-block;
				vertical-align: top;
				line-height: 14px;

				.type, .val {
					display: inline-block;
					vertical-align: top;
					font-size: $bodyText5;
				}

				.type {
					margin-right: 7px;
					color: $colorLightGray;
				}

				+ li {
					margin-left: 11px;
				}
			}
		}

		.review-desc {
			font-size: $bodyText5;
			@include multi_ellipsis(22px, 1);
			height: auto;
			margin-top: 10px;
			color: $colorDarkGray;

			p {
				display: inline-block;
			}

			.review-desc-img {
				display: none;

				img {
					display: block;
					max-width: 100%;

					+ img {
						margin-top: 10px;
					}
				}
			}
		}

		.profile-name {
			@include justify_width(start, true);
			padding-bottom: 1px;
			margin-top: 10px;
			max-width: 100%;

			.fullname {
				flex: 1;
				@include ellipsis;
				overflow: hidden;
				width: auto;
				max-width: none;
				font-weight: 700;
				margin-right: 6px;
			}

			.date {
				margin-top: 1px;
				font-size: 1rem;
				line-height: 1.3rem;
				color: $colorLightGray;
			}
		}
	}

	.prod-review-img-thumb {
		flex: 0 0 80px;
		margin-left: 74px;

		.review-img-thumb {
			width: 80px;
			height: 80px;
		}
	}

	&.expanded {
		.review-info-wrap {
			.review-desc {
				@include clear_multi_ellipsis;

				p {
					display: block;
				}

				.review-desc-img {
					display: block;
					margin-top: 15px;
				}
			}
		}

		.prod-review-img-thumb {
			display: none;
		}
	}

}

.review-img-thumb {
	position: relative;
	display: inline-block;
	width: 120px;
	height: 120px;
	background: {
		color: #ccc;
		repeat: no-repeat;
		position: center;
		size: cover;
	}
}

//e : 리뷰 박스

.list-sort-wrap {
	padding-bottom: 10px;
	border-bottom: 1px solid $colorBlack;;
	@include justify_width;
	align-items: center;

	.sort-rdo-wrap {
		flex: 1;

		.ui-rdo input[type=radio] + label {
			font-size: $bodyText5;
			line-height: 1.8rem;
		}
	}

	.btn-link {
		font-size: $bodyText4;
		font-style: inherit;
	}
}

.coupon-item {
	position: relative;
	display: block;
	height: 135px;
	margin: 0 5px;
	padding: 15px 15px;
	background: #fff;
	box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.1);
	border: {
		top: solid 1px $colorLightenGray;
		bottom: solid 1px $colorLightenGray;
	}

	&:after,
	&:before {
		z-index: 3;
		content: '';
		position: absolute;
		top: -1px;
		bottom: -1px;
		display: block;
		width: 6px;
		height: 135px;
		border: {
			top: solid 1px $colorLightenGray;
			bottom: solid 1px $colorLightenGray;
		}
	}

	&:after {
		right: -5px;
		@include background_img('../images/common/comm_bg_coupon_r.png', 12px, 266px);
	}

	&:before {
		left: -5px;
		@include background_img('../images/common/comm_bg_coupon_l.png', 12px, 266px);
	}

	.coupon-item-flex {

		.coupon-type, .coupon-value, .coupon-site-info {
			display: block;
			text-align: center;
		}

		.coupon-type {
			font-size: $bodyText6;
			line-height: 1.4rem;
		}

		.coupon-value {
			margin-top: 14px;
			font-size: 3.6rem;
			line-height: 4.5rem;
			font-style: italic;
		}

		.coupon-site-info {
			margin-top: 1.8rem;
			font-size: $bodyText6;
			line-height: 1.4rem;
		}
	}

}
