@import 'scss_header';

// s : jQueryUI Dialog
.dialog-open {
	position: fixed;
	overflow: hidden !important;
	touch-action: none;
	height: 100%;

	> .popup-target-inner {
		height: 100%;
		overflow: {
			x: auto;
			y: scroll;
		}
	}

	.os-viewport {
		overflow: hidden !important;
	}

	&.ios {
		&.standalone, &.webview {
			overflow: {
				x: hidden !important;
				y: scroll !important;
			}
		}
	}
}

.ui-dialog-container {
	//position: absolute;
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 900;
	display: none;
	overflow: hidden;
	outline: 0;
	height: 100vh;

	&.open {
		z-index: 1001;
		display: block;
		overflow: hidden;
		-webkit-overflow-scrolling: initial;
		touch-action: none;

		.ui-dialog-contents {
			display: block;
		}
		.ui-dialog {
			&.dialog-alert, &.dialog-notice, &.dialog-loading, &.dialog-full, &.dialog-full-contentdetail {
				&:before {
					opacity: 1;
					transition: opacity 0.5s $transDefaultEase;
				}
			}

		}
		.ui-dialog-container-inner {
			.btn-close-dim {
				opacity: 1;
				transition: opacity 0.5s $transDefaultEase;
			}
		}
	}

	&.full {

		.ui-dialog-contents {
			.btn-even-wrap {
				margin-top: 20px;
			}
		}
	}

	&.keword-type {
		position: absolute;
		height: calc(100% - 90px) !important;

		.ui-dialog {
			max-height: 100%;

			.ui-dialog-titlebar {
				display: block;
			}
		}
	}
}

.ui-dialog-container-inner {
	z-index: 2;
	position: relative;
	display: -webkit-box;
	display: -ms-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-line-pack: center;
	align-content: center;
	height: 100%;
	-webkit-transform: translate(0, 0);
	transform: translate(0, 0);
	overflow: hidden;

	.ui-widget-overlay {
	}

	.btn-close-dim {
		opacity: 0;
		z-index: 1;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100vw;
		overflow: hidden;
		background: rgba(255, 255, 255, 0.6);
		-webkit-overflow-scrolling: initial;
		touch-action: none;
	}
}

.ui-dialog {
	z-index: 2;
	position: static;
	max-width: 100%;
	background: #fff;
	padding: 0;
	//margin: 0;
	border: 1px solid #000;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	flex-direction: column;
	max-height: 100vh;
	width: 100% !important;

	.ui-dialog-titlebar {
		/*position: absolute;
		min-height: 45px;
		*/
		text-align: center;
		padding: 0;

		.ui-dialog-title {
			display: inline-block;
			float: none;
			width: 100%;
			margin: 0;
			text-align: left;
			font-size: $bodyText4;
			line-height: 2rem;
			text-overflow: initial;
			overflow: visible;
			white-space: initial;
		}

		.ui-dialog-titlebar-close {
			width: 20px;
			height: 20px;
			margin: 0;
			padding: 0;
			@include background_img('../images/ui/button/ui_btn_popup_close.png', 40px, 40px);
			top: 10px;
			right: 10px;
		}
	}

	.ui-dialog-contents {
		padding: 0;

		.tbl-wrap {
			&.tbl-row {
				border: none;
				padding: 0;

				table {
					tr {
						&.input {
							&:first-child {
								th {
									padding-top: 10px;
								}

								td {
									padding-top: 0;
								}
							}

							&:last-child {
								th {
								}

								td {
									padding-bottom: 0;
								}
							}

							.desc-gray, .desc-black {
								margin-top: 6px;
							}

							// s : 레이어 팝업 > 우편번호 찾기
							.zip-code-wrap {
								height: 0;
								margin-top: 0;
								border: 0 solid transparent;

								.zip-code-header {
									@include justify_width;
									padding: 17px 17px 0 19px;
									//opacity: 0;

									.zip-code-title {
										@include calc_letter_spacing(-50, $bodyText3);
										font-weight: 500;
										line-height: $bodyText1;
									}

									.btn-zipcode-close {
										width: 20px;
										height: 20px;
										background: url(../images/ui/button/ui_btn_popup_close.png) no-repeat center;

										span {
											@include hidden;
										}
									}
								}

								.zip-code-content {
									//opacity: 0;
									overflow-y: auto;
									height: calc(100% - 54px);
									margin: 17px 19px 0;
								}

								&.is-active {
									height: 375px;
									margin-top: 10px;
									border: 1px solid $colorBlack;
									//transition: all .23s ease-in-out;

									.zip-code-header {
										//opacity: 1;
										//transition: all .23s ease-in-out;
									}

									.zip-code-content {
										//opacity: 1;
										//transition: all .23s ease-in-out;
									}
								}
							}
						}
					}
				}
			}
		}

		.ui-list {
			font-size: 0;

			li {
				display: inline-block;
				vertical-align: top;

				+ li {
					margin-left: 20px;
				}
			}
		}

		.form-list {
			.form-cont {
				.input-wrap {
					width: 100%;
				}
			}
		}

		//s : 단순 텍스트 노출시
		.dialog-desc-box {
			.desc {
				font-size: $bodyText4;
				line-height: 2rem;
				text-align: center;

				+ .desc {
					margin-top: 20px;
				}
			}
		}

		//e : 단순 텍스트 노출시

		.btn-even-wrap {
			margin-top: 30px;
		}

		&:not(.initialized) {
			display: none;
		}
	}

}

// e : jQueryUI Dialog

// s: 별점
.rating-container {
	font-size: $bodyText1;

	.rating-stars {
		vertical-align: top;

		.empty-stars,
		.filled-stars {
			display: block;
			height: 20px;
			min-width: 20px !important;
		}

		.star {
			vertical-align: top;
			margin: 0;

			.glyphicon {
				display: block;

				&:before {
					content: '';
					display: block;
					overflow: hidden;
					width: 20px;
					height: 20px;
					@include background_img('../images/ui/form/ui_rating_star.png', 80px, 40px);
					background-position: right 0 !important;
				}

				&.glyphicon-star {
					&:before {
						background-position: 0 0 !important;
					}
				}
			}

			~ .star {
				margin-left: 6px;
			}
		}
	}

	/* s: sm size */
	&.rating-sm {
		.rating-stars {
			.empty-stars,
			.filled-stars {
				height: 12px;

				.star {
					.glyphicon {
						&:before {
							width: 12px;
							height: 12px;
							@include background_img('../images/ui/form/ui_rating_star_sm.png', 48px, 24px);
						}
					}
				}
			}
		}

	}

	/* e: sm size */

	/* s: sm size(gray) */
	&.rating-sm-gray {
		height: 12px;

		.rating-stars {
			.empty-stars,
			.filled-stars {
				height: 12px;

				.star {
					.glyphicon {
						&:before {
							width: 12px;
							height: 12px;
							@include background_img('../images/ui/form/ui_rating_star_sm_gray.png', 48px, 24px);
						}
					}

					~ .star {
						margin-left: 1px;
					}
				}
			}
		}

	}

	/* e: sm size(gray) */

	/* s: 비활성화 */
	&.rating-disabled {
		.rating-input, .rating-stars {
			cursor: default;
		}
	}

	/* e: 비활성화 */
}

// e: 별점

// s: Spinner
.ui-spinner {
	display: inline-block;
	vertical-align: top;
	padding: 0 20px;

	.ui-spinner-input {
		width: 34px;
		height: 20px;
		padding: 1px 3px 0;
		margin: 0;
		vertical-align: top;
		text-align: center;
		color: $colorBlack;
		font-family: $fontEng;
		@include calc_letter_spacing(50, $bodyText4);

		&.disabled {
			border: none;
			opacity: 1;
			background-color: #fff;
		}
	}

	.ui-spinner-button {
		width: 20px;
		height: 20px;
		@include background_img('../images/ui/button/ui_btn_toggle.png', 80px, 40px);
		background-position: right 0;
		cursor: pointer;
		top: 0;
		bottom: inherit;

		&.ui-spinner-up {
			right: 0;
			background-position: 0 0;
		}

		&.ui-spinner-down {
			left: 0;
		}

		&.ui-button-disabled {
			opacity: 0.5;
		}
	}
}

// e: Spinner

.ui-dialog-contents {
	display: none;
}
