@import 'scss_header';

// s : IE 버튼 클릭 이펙트 제거
button {
	.inner-text {
		position: relative;
	}
}

// e : IE 버튼 클릭 이펙트 제거

// s: bottom fix button
.btn-arrow-back,
.btn-arrow-top {
	position: relative;
	display: block;
	width: 36px;
	height: 36px;
	background: $colorBlack;

	span {
		@include offscreen;
	}

	&:before {
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		@include background_img('../images/ui/button/ui_footer_btn_arrow.png', 60px, 60px);
		background-position: center center;
	}


}


.btn-arrow-back:before {
	transform: rotate(-90deg);
}


.btn-control-view {
	position: relative;
	width: 50%;
	padding: 0 0 0 16px;
	font-size: $bodyText6;
	text-align: left;
	color: $colorBlack;
	background: #fff;
	text-transform: uppercase;

	border: 1px solid $colorBlack;
	box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.1);

	&:after {
		content: '';
		position: absolute;
		top: 16px;
		right: 15px;
		display: block;
		width: 9px;
		height: 9px;
		@include background_img('../images/ui/button/ui_btn_prod_control.png', 36px, 18px);
	}

	~ .btn-control-view {
		border-left: none;
	}

	&:hover {
		background-color: $colorLightestGray;
	}

	&:disabled, a.disabled {
		color: $colorLightenGray;
		background-color: $colorLightestGray;
		pointer-events: none;
		border-color: $colorLightGray;

		&:after {
			opacity: 0.5;
		}

	}
}

// e: bottom fix button
// s: header
.hbtn-arrow-back {
	position: relative;
	display: block;
	width: 30px;
	height: 30px;

	span {
		@include offscreen;
	}

	&:before {
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		@include background_img('../images/ui/button/ui_header_btn_back.png', 60px, 60px);
		background-position: center center;
	}

}

.hbtn-text {
	display: inline-block;
	font-size: 1.5rem;
	font-weight: 300;
	color: #000;
	text-transform: uppercase;

	~ .hbtn-text {
		margin-left: 15px;
	}
}

// s : 공통 버튼 ( min-width: 100px / height : 40px )
.btn {
	overflow: hidden;
	position: relative;
	min-width: 100px;
	border: 1px solid #fff;
	text-align: center;
	font-weight: 400;
	font-family: $fontMix;
	font-size: $bodyText6;
	height: 40px;
	line-height: 1.4rem;
	padding: 3px 15px;
	color: $colorBlack;
	background-color: #fff;
	transition: background-color 0.5s;
	-webkit-transition: background-color 0.5s;

	display: inline-flex;
	justify-content: center;
	align-items: center;


	&:hover, &:focus {
		background-color: #eee;
		border-color: #eee;
	}

	&[disabled], &.disabled {
		color: $colorLightenGray;
		background-color: #fff;
		border-color: #fff;
		pointer-events: none;
	}

}

.btn-sm {
	overflow: hidden;
	position: relative;
	min-width: 90px;
	border: 1px solid $colorLightenGray;
	text-align: center;
	font-weight: 400;
	font-family: $fontMix;
	font-size: $bodyText6;
	height: 35px;
	line-height: 1.4rem;
	padding: 3px 15px;
	color: $colorGray;
	background-color: #fff;
	transition: background-color 0.5s;
	-webkit-transition: background-color 0.5s;

	display: inline-flex;
	justify-content: center;
	align-items: center;


	&:hover, &:focus {
		background-color: #eee;
	}

	&[disabled], &.disabled {
		color: $colorLightenGray;
		background-color: #fff;
		border-color: $colorLightenGray;
		pointer-events: none;
	}

	@include mob_small {
		padding: 0 10px;
	}
}

// e : 공통 버튼 ( min-width: 100px / height : 40px )

// s: 버튼 컬러 지정
.btn-gray {
	color: $colorBlack;
	background-color: $colorLightenGray;
	border-color: $colorLightenGray;

	&:hover, &:focus {
		border-color: $colorLightGray;
		background-color: $colorLightGray;
	}

	&[disabled], &.disabled {
		color: $colorLightenGray;
		background-color: #fff;
		border-color: #fff;
	}
}

.btn-black {
	border-color: $colorBlack;
	background-color: $colorBlack;
	color: #fff;

	&:hover, &:focus {
		background-color: $colorDarkGray;
	}

	&[disabled], &.disabled {
		background-color: $colorLightenGray;
		border-color: $colorLightenGray;
		color: #fff;
	}
}

.btn-lnblack {
	color: $colorBlack;
	border-color: $colorBlack;

	&:hover, &:focus {
		border-color: $colorBlack;
		background-color: #eee;
	}

	&[disabled], &.disabled {
		color: $colorLightenGray;
		border-color: $colorLightenGray;
	}
}

.btn-lngray {
	color: $colorDarkGray;
	border-color: $colorLightenGray;
	background-color: #fff;

	&:hover, &:focus {
		border-color: $colorLightenGray;
		background-color: #eee;
	}

	&[disabled], &.disabled {
		color: $colorLightenGray;
		border-color: $colorLightenGray;
	}
}

// e: 버튼 컬러 지정

//s : 버튼 폰트 지정
.btn-text-lg {
	font-size: $bodyText3;
}

//e : 버튼 폰트 지정

// s: 버튼 wrap

// 균등형 wrap
.wrap {
	.contents-wrap {
		.cont-padding {
			> .btn-even-wrap {
				margin-top: 20px;
			}
		}
	}
}

.btn-even-wrap {
	margin-top: 20px;
	@include justify_width;
	flex-wrap: wrap;
	-webkit-flex-wrap: wrap;

	.btn, [class*=btn-] {
		flex: 1;
		min-width: auto;

		+ [class*=btn-] {
			border-left-width: 0;
			&.full {
				margin-top: 10px;
				width: 100%;
				min-width: 100%;
				border-left-width: 1px;
			}
		}
	}

	&.btn-gap {
		.btn, [class*=btn-] {
			+ .btn, + [class*=btn-] {
				border-left-width: 1px;
				margin-left: 5px;
			}

			&.ratio-1x {
				flex: 0 1 calc((100% - 10px) / 3);
			}
		}
	}

	&.text-left {
		.btn, [class*=btn-] {
			justify-content: left;
		}
	}

	+ .btn-even-wrap {
		margin-top: 10px !important;
	}
}

// e: 버튼 wrap


// s : 특정 스타일의 버튼

.btn-coupon-download { //쿠폰 다운로드
	display: inline-block;
	height: 25px;
	padding: 0 10px;
	background: $colorLightestGray;
	border: 1px solid $colorLightGray;
	text-align: center;
	font-size: $bodyText6;
	font-style: italic;
	font-family: $fontMix;
	line-height: 23px;

	.inner-text {
		font-style: italic;
	}
}

.btn-item-equal { //equl 버튼
	width: 36px;
	height: 36px;
	padding: 3px;
	background-color: rgba(#fff, .7);

	span {
		@include offscreen;
	}

	&:after {
		content: '';
		width: 30px;
		height: 30px;
		@include background_img('../images/ui/button/ui_btn_equal.png', 120px, 60px,);
	}

	&.selected {
		&:after {
			background-position: -30px 0;
		}
	}
}

.btn-item-share { //공유하기 버튼
	width: 36px;
	height: 36px;
	padding: 3px;
	font-size: 0;

	span {
		@include offscreen;
	}

	&:after {
		content: '';
		width: 30px;
		height: 30px;
		@include background_img('../images/ui/button/ui_btn_share.png', 60px, 60px,);
	}
}

.btn-more { // 더보기
	display: block;
	width: 100%;
	text-align: center;
	margin: 20px 0;
	font-size: $bodyText5;
	font-family: $fontEng;
	text-decoration: underline;
	&.top-line {

	}
	&.no-btm-mg {
		margin-bottom: 0;
	}
}

.btn-content-detail { // learn more
	position: relative;
	@include justify_width(start, true);
	flex-direction: column;
	justify-content: center;
	min-width: 190px;
	width: 190px;
	height: 50px;
	padding-left: 50px;
	background-color: #fff;
	text-align: left;
	box-shadow: -10px 10px 10px 0 rgba(0, 0, 0, 0.05);
	-webkit-box-shadow: -10px 10px 10px 0 rgba(0, 0, 0, 0.05);
	-moz-box-shadow: -10px 10px 10px 0 rgba(0, 0, 0, 0.05);

	.subject {
		display: block;
		font-family: $fontEng;
		font-weight: 500;
		letter-spacing: -0.03rem;
		font-size: $bodyText5;
		line-height: 1;
	}

	.description {
		display: block;
		margin-top: 7px;
		font-family: $fontKor;
		font-size: 1rem;
		color: $colorGray;
		line-height: 1;
	}

	&:before {
		content: '';
		position: absolute;
		top: 10px;
		left: 10px;
		display: block;
		width: 30px;
		height: 30px;
		@include background_img('../images/ui/button/ui_btn_detail_arrow.png', 60px, 60px);
	}
}

.btn-prod-detail { // product +
	position: relative;
	display: block;
	min-width: 95px;
	height: 35px;
	padding-left: 33px;
	background-color: rgba(0, 0, 0, .5);
	border-radius: 19px;
	font-size: 1rem;
	font-family: $fontEng;
	line-height: 35px;
	text-align: left;
	color: #fff;
	letter-spacing: 0.05rem;
	transition: background-color 0.5s;
	-webkit-transition: background-color 0.5s;

	&:before {
		content: '';
		position: absolute;
		top: 12px;
		left: 11px;
		width: 12px;
		height: 12px;
		@include background_img('../images/ui/button/ui_btn_detail_plus.png', 24px, 24px);
	}

	&:hover, &:focus {
		background-color: rgba(0, 0, 0, .7);
	}

	&:disabled, .disabled {
		background-color: rgba(0, 0, 0, .5);
	}
}

.btn-link, a.btn-link { // 텍스트 링크
	display: inline-block;
	text-decoration: underline !important;
	text-transform: uppercase;
	font-family: $fontMix;
	@include calc_letter_spacing(0, $bodyText6);
	line-height: 1;
	color: $colorBlack;
	vertical-align: 0;
}


// 기간 설정 버튼
.date-range-wrap {
	position: relative;

	.btn-ip {
		position: relative;
		display: inline-block;
		width: calc((100% + 3px) / 4);

		input[type=checkbox], input[type=radio] {
			@include offscreen;

			+ .btn-ip-view {
				overflow: hidden;
				position: relative;
				display: inline-block;
				width: 100%;
				border: 1px solid $colorLightenGray;
				text-align: center;
				font-weight: 400;
				font-family: $fontMix;
				height: 40px;
				padding: 0 10px;
				line-height: 38px;
				color: $colorLightGray;
				background: #fff;
				border-right-color: transparent;
				font-size: $bodyText4;
				letter-spacing: -0.033rem;

				&[disabled], &.disabled {
					color: $colorLightenGray;
					background-color: #fff;
					border-color: $colorLightenGray;
					pointer-events: none;
				}
			}

			&:checked {
				+ .btn-ip-view {
					z-index: 2;
					color: $colorBlack;
					border: 1px solid $colorBlack;
					transition: border 0.5s;
					-webkit-transition: border 0.5s;
				}
			}
		}

		& ~ .btn-ip {
			margin-left: -1px;
		}

		&:last-child input + .btn-ip-view {
			border-right-color: $colorLightenGray;
		}
	}

	.btn {
		position: relative;
		color: $colorLightGray;
		width: 100px;
		min-width: 100px;
		background: #fff;
		border: 1px solid #ccc;
		@include calc_letter_spacing(-50, 1.3rem);

		+ .btn {
			margin-left: -1px;
		}

		&.active {
			color: $colorBlack;
			border-color: $colorBlack;
			z-index: 1;
		}
	}
}

.btn-sns-share {
	position: relative;
	display: inline-block;
	width: 50px;
	height: 50px;
	border: 1px solid $colorLightenGray;
	border-radius: 100%;
	background-color: #fff;
	vertical-align: top;

	span {
		@include offscreen;
	}

	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		display: block;
		width: 30px;
		height: 21px;
		@include background_img('../images/ui/button/ui_btn_sns_share.png', 300px, 42px);
		transform: translate(-50%, -50%);
	}

	&.naver {
		&:after {
			background-position: 0 0;
		}
	}

	&.twitter {
		&:after {
			background-position: -30px 0;
		}
	}

	&.facebook {
		&:after {
			background-position: -60px 0;
		}
	}

	&.kakao {
		&:after {
			background-position: -90px 0;
		}
	}

	&.url {
		&:after {
			background-position: -120px 0;
		}
	}

	~ .btn-sns-share {
		margin-left: 10px;
	}
}

.btn-alert {
	width: 19px;
	height: 19px;
	vertical-align: top;
	@include background_img('../images/ui/button/ui_btn_ico_alert.png', 38px, 38px);
	display: inline-block;
}

// e : 특정 스타일의 버튼

