@import 'scss_header';

.ui-rdo, .ui-chk {
	position: relative;
	display: inline-block;
	min-height: 12px;
	font-size: 0;
	vertical-align: top;

	input {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		font-size: $bodyText6;
		border: 0;

		&:checked + label {
			color: $colorBlack;

			&:before {
			}

			&:after {
				opacity: 1;
			}
		}

		&:disabled {
			+ label {
				color: $colorLightGray;
				pointer-events: none;

				&:before {
					background-color: #f8f8f8;
					border-color: #ddd;
				}
			}
		}

		&:hover + label:before {
		}

		+ label {
			display: inline-block;
			position: relative;
			color: $colorGray;
			padding: 0 0 0 22px;
			font-size: $bodyText4;
			vertical-align: middle;
			line-height: 2rem;
			cursor: pointer;
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			-webkit-transition: color $transDefaultTime $transDefaultEase;
			transition: color $transDefaultTime $transDefaultEase;
			text-transform: uppercase;
			word-break: keep-all;
			word-wrap: break-word;

			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 2px;
				width: 16px;
				height: 16px;
				line-height: 16px;
				text-align: center;
				background: #fff;
				border: 1px solid $colorLightenGray;
				-webkit-box-sizing: border-box;
				box-sizing: border-box;

				-webkit-transition: border-color $transDefaultTime $transDefaultEase, border-width $transDefaultTime $transDefaultEase;
				transition: border-color $transDefaultTime $transDefaultEase, border-width $transDefaultTime $transDefaultEase;
			}

			&:after {
				content: '';
				position: absolute;
				top: 3px;
				width: 0;
				height: 0;
				line-height: 10px;
				text-align: center;
				-webkit-box-sizing: border-box;
				box-sizing: border-box;
				opacity: 0;
				z-index: 1;
			}
		}
	}

	+ .ui-rdo, + .ui-chk {
		margin-left: 22px;
	}

	&.ui-chk-info-same {
		margin: 10px 0;
	}
}

.ui-rdo-box {
	display: block;

	input {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		font-size: $bodyText6;
		border: 0;

		&:checked + label {
			z-index: 2;
			color: $colorBlack;
			border-color: $colorBlack;
		}

		&:disabled {
			+ label {
				color: $colorLightGray;
				pointer-events: none;

				&:before {
					background-color: #f8f8f8;
					border-color: #ddd;
				}
			}
		}

		~ label {
			z-index: 1;
			overflow: hidden;
			position: relative;
			width: 100%;
			border: 1px solid $colorLightenGray;
			text-align: center;
			font-weight: 400;
			font-family: $fontMix;
			font-size: $bodyText6;
			height: 40px;
			line-height: 1.4rem;
			padding: 3px 15px;
			color: $colorGray;
			background-color: #fff;
			transition: border 0.5s;
			display: inline-flex;
			justify-content: center;
			align-items: center;

			&[disabled], &.disabled {
				opacity: 0.5;
				pointer-events: none;
			}
		}
	}
}

.ui-col-list {
	li {
		.ui-rdo, .ui-chk {
			padding: 10px 15px 10px 0;
		}
	}

	> li {
		> .ui-rdo, > .ui-chk {
			position: relative;
			width: 100%;
			padding-left: 3px;

			&:before {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				height: 1px;
				background-color: $colorLightestGray;
			}
		}
	}
}

/* e : 폼 - 라디오, 체크박스 버튼 공통 */

/* s : 폼 - 라디오 버튼 */
.ui-rdo {

	input[type=radio] {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;
		font-size: $bodyText6;

		+ label {

			&:before {
				border-radius: 50%;
			}

			&:after {
				left: 6px;
				top: 3px;
			}
		}

		&:checked + label {
			&:after {
				width: 14px;
				height: 14px;
				left: 1px;
				background: #000;
				border-radius: 50%;
				opacity: 1;
			}
		}

		&:checked {
			&:disabled {
				+ label {
					&:after {
						opacity: 0;
					}
				}
			}
		}
	}

	@at-root .sort-rdo-wrap & {
		input[type=radio] {
			+ label {
				color: $colorLightGray;
				padding: 0;
				font-size: $bodyText5;

				&:before {
					content: none;
				}

				&:after {
					content: none;
				}
			}

			&:checked + label {
				color: $colorBlack;
			}
		}

		+ .ui-rdo {
			margin-left: 15px;
		}
	}
}

/* e : 폼 - 라디오 버튼 */

/* s : 폼 - 체크박스 */
.ui-chk {
	input[type=checkbox] {
		+ label:after {
			left: 5px;
		}

		&:checked + label:after {
			width: 14px;
			height: 14px;
			background: #000;
			left: 1px;
		}

		&:checked {
			&:disabled {
				+ label {
					&:after {
						opacity: 0;
					}
				}
			}
		}

		+ label {
			.point {
				color: #000;
			}
		}
	}
}

/* s : 폼 - 인풋 */
.ui-input {
	&[type=date], &[type=number], &[type=text], &[type=password], &[type=search], &[type=email] {
		display: inline-block;
		width: 100%;
		height: 40px;
		padding: 0 10px;
		border: 1px solid $colorLightenGray;
		font-family: $fontKor;
		font-size: $bodyText5;
		@include set_placeholder_color(#aaa);

		&:focus {
			border-color: #000;
		}

		&[disabled], &[readonly] {
			border-color: #ddd;
			background: #f8f8f8;
			color: #aaa !important;
		}
	}
}

/* e : 폼 - 인풋 */

/* s : 폼 - date */
.date-box {
	position: relative;

	.ui-cal {
		&[type=date] {
			display: inline-block;
			width: 100%;
			height: 40px;
			padding: 0 10px;
			border: 1px solid $colorLightenGray;
			font-family: $fontKor;
			font-size: $bodyText5;
			@include set_placeholder_color(#aaa);
			background-color: #fff;
			line-height: 4rem;


			&[disabled], &[readonly] {
				border-color: #ddd;
				background: #f8f8f8;
				color: #aaa !important;
			}

			&:focus {
				border-color: #000;
			}

			&:after {
				content: '';
				position: absolute;
				top: 10px;
				right: 15px;
				display: block;
				width: 20px;
				height: 20px;
				@include background_img('../images/ui/button/ui_btn_calendar.png', 40px, 40px);
			}

			&::-webkit-inner-spin-button {
				display: none;
			}

			&::-webkit-calendar-picker-indicator {
				position: absolute;
				top: 1px;
				left: 1px;
				display: block;
				width: calc(100% - 2px);
				height: 38px;
				background-color: transparent !important;
				border: none;
				padding: 0;
				margin: 0;
				color: rgba(0, 0, 0, 0);
				opacity: 1;
				z-index: 2;
			}
		}
	}
}

/* e : 폼 - date */

/* s : 폼 - select */
select.ui-sel {
	width: 100%;
	height: 40px;
	position: relative;
	color: $colorBlack;
	margin: 0;
	padding: 0 36px 0 15px;
	border: 1px solid $colorLightenGray;
	line-height: 4rem;
	font-size: $bodyText4;
	@include background_img('../images/ui/form/ui_sel_arrow.png', 40px, 40px);
	background-position: calc(100% - 15px) center;
	background-color: #fff;
}

/* e : 폼 - select */

// s : 입력글자 제한 textarea
.ip-byte-check {
	height: 160px;

	textarea {
		padding: 7px 10px;
		height: calc(100% - 27px);
		border-bottom: none;
		border-color: $colorLightenGray;
		@include calc_letter_spacing(0, $bodyText5);
		@include set_placeholder_color(#aaa);
		resize: none;
		line-height: 2rem;

		&:focus {
			outline: none;
		}
	}

	.byte {
		display: block;
		height: 27px;
		padding-top: 5px;
		padding-right: 9px;
		background-color: #fff;
		border: 1px solid $colorLightenGray;
		border-top: none;
		font-size: $bodyText6;
		text-align: right;
		color: #aaa;
		font-family: $fontEng;
		@include calc_letter_spacing(0, $bodyText6);
		line-height: 1;

		span {
			color: $colorBlack;
		}
	}

	&.disabled {
		background-color: #f8f8f8;

		textarea {
			border-color: $colorLightenGray;
			border-bottom: none;
		}

		.byte {
			background-color: #f8f8f8;

			span {
				color: #aaa;
			}
		}
	}

	&.md {
		height: 100px;
	}
}

// e : 입력글자 제한 textarea

// s : 커스텀 셀렉트
.ui-selectmenu-menu {
	// s : 상품 옵션 셀렉트
	.ui-menu.select-prod-option {

		.ui-menu-item {
			display: flex;
			line-height: 26px;

			.ui-menu-item-wrapper.prod-option {
				padding: {
					top: 6px;
					bottom: 6px;
				}
				@include justify_width;
				align-items: center;
				flex: 1;

				.option-label-box-inner {

				}

				.option-stock-box {
					display: inline-block;
					font-size: $bodyText6;
					color: $colorGray;
					margin-left: 10px;
					vertical-align: top;
				}
			}

			&.ui-state-disabled {
				.ui-menu-item-wrapper.prod-option {
					.option-label-box-inner {
						color: $colorLightGray;
						text-decoration: line-through;
					}

					.option-stock-box {
						display: none;
					}

					.option-label-box {
						&:after {
							content: 'Sold Out';
							display: inline-block;
							color: $colorGray;
							margin-left: 10px;
							font-size: $bodyText6;
							text-decoration: none;
							text-transform: uppercase;
							font-family: $fontEng;
							vertical-align: top;
						}
					}
				}
			}
		}
	}

	// e : 상품 옵션 셀렉트
}

// e : 커스텀 셀렉트

//s : 유효성 체크
.valid-check {

	.valid-desc {
		display: none;
	}

	.ui-input {
		margin: 0 !important;
	}

	&.fail {
		.ui-input, .select-wrap .ui-selectmenu-button.ui-button {
			color: $colorDarkGray;
		}

		.valid-desc {
			display: block;
			margin-top: 3px;
			font-size: 1rem;
			line-height: 1.5rem;
		}
	}
}

//e : 유효성 체크

//s : 기간 설정
.search-period-wrap {
	padding: 20px 0 0;

	.search-period-form {
		.date-range-wrap {
			margin-bottom: 10px;
		}

		.period-wrap {
			@include justify_width;
			align-items: center;

			.date-box {
				flex: 1;
			}

			.gap {
				display: inline-block;
				flex: 0 1 18px;
				text-align: center;
				font-size: $bodyText5;
				color: #aaa;
			}
		}
	}

	.btn-even-wrap {
		margin-top: 10px;
	}
}

//e : 기간 설정

/* s : 폼 - 파일첨부 */
.file-wrap {
	position: relative;
	display: inline-block;
	margin-right: 10px;

	.btn-box {
		position: relative;
		display: inline-block;
		vertical-align: top;

		input[type=file] {
			/* 파일 필드 숨기기 */
			position: absolute;
			width: 1px;
			height: 1px;
			padding: 0;
			margin: -1px;
			overflow: hidden;
			clip: rect(0, 0, 0, 0);
			border: 0;

			//&:disabled {
			//
			//	+ label {
			//		background-color: #a3a3a3;
			//		border: 1px solid #a3a3a3;
			//		color: #d6d6d6;
			//
			//		&:hover {
			//			cursor: default;
			//		}
			//	}
			//}
		}

		label {
			font-size: 0 !important;
		}
	}

	&.img {
		.btn-box {
			width: 58px;
			height: 58px;
		}

		label {
			display: inline-block;
			width: 100%;
			height: 100%;
			@include background_img('../images/ui/form/file_img_icon_plus.png', 40px, 40px);
			background-position: center;
			border: 1px solid $colorLightenGray;
			cursor: pointer;
			vertical-align: top;
		}

		.attach-img-box {
			position: absolute;
			display: none;
			width: 100%;
			height: 100%;
			//pointer-events: none;
			left: 0;
			top: 0;
			z-index: 1;

			.attach-img-view {
				position: absolute;
				display: inline-block;
				width: 100%;
				height: 100%;
				background-position: center;
				background-size: cover;
				font-size: 0 !important;
				left: 0;
				top: 0;
			}

			.btn-remove-img {
				position: absolute;
				width: 20px;
				height: 20px;
				@include background_img('../images/ui/form/file_img_icon_remove.png', 40px, 40px);
				font-size: 0 !important;
				pointer-events: auto;
				right: 0;
				top: 0;
			}
		}

		&.attached {
			.attach-img-box {
				display: inline-block;
			}
		}

		&:last-of-type {
			margin-right: 0;
		}
	}
}

/* e : 폼 - 파일첨부 */

/* s: search input form */
.search-input-form {
	overflow: hidden;
	height: 45px;
	padding: 0 5px;
	background-color: $colorLightestGray;
	@include justify_width(center);
	align-items: center;

	.ui-input {
		flex: 1;
		background: transparent;
		border: 0;
		height: 45px;
		line-height: 4.5rem;
		font-size: 1.6rem;
		@include set_placeholder_color($colorLightenGray);
		@include mob_small {
			font-size: 1.5rem;
		}
	}

	.btn-search {
		flex: 0 1 40px;
		height: 40px;
		text-align: center;

		&:before {
			content: '';
			@include background_img('../images/common/comm_search_btn.png', 60px, 60px);
			width: 30px;
			height: 30px;
			display: inline-block;
		}
	}

	.btn-reset-input {
		@include background_img('../images/ui/button/ui_btn_popup_close.png', 40px, 40px);
		display: none;
		width: 20px;
		height: 20px;
		margin: 0;
		padding: 0;

		&.is-active {
			display: block;
		}
	}

	.btn-input-search {
		position: relative;
		display: block;
		width: 100%;
		height: 45px;
		padding: 0 50px 0 10px;
		line-height: 4.5rem;
		font-size: 1.6rem;
		color: $colorLightenGray;
		text-align: left;
		@include set_placeholder_color($colorLightenGray);
		@include mob_small {
			font-size: 1.5rem;
		}
		&:after {
			content:'';
			display: block;
			position: absolute;
			top: 50%;
			right: 0;
			margin-top: -20px;
			width: 40px;
			height: 40px;
			@include background_img('../images/common/comm_search_btn.png', 60px, 60px);
			background-position: center;
		}
	}
}

/* e: search input form */

// s : 우편번호 찾기
.zip-code-wrap {
	border: 1px solid $colorBlack;

	.zip-code-header {
		@include justify_width;
		padding: 10px 11px 0 15px;
		//opacity: 0;

		.zip-code-title {
			padding-top: 5px;
			font-size: $bodyText5;
			line-height: 2rem;
		}

		.btn-zipcode-close {
			width: 20px;
			height: 20px;
			@include background_img('../images/ui/button/ui_btn_popup_close.png', 40px, 40px);

			span {
				@include hidden;
			}
		}
	}

	.zip-code-content {
		color: $colorBlack;
		overflow-y: auto;
		padding: 0 15px 15px;
	}

	.btn-flex-wrap {
		width: 100%;

		.zipcode-search-btn {
			min-width: 60px;
		}
	}
}

.zipcode-guide {
	margin-top: 10px;

	dt {
		font-size: $bodyText6;
		line-height: 1.8rem;
		color: $colorBlack;
	}

	dd {
		margin-top: 10px;

		p {
			font-size: $bodyText6;
			line-height: 1.7rem;
			color: $colorDarkGray;

			+ p {
				margin-top: 9px;
			}
		}

		span {
			display: block;
			margin-top: 3px;
			font-size: $bodyText6;
			line-height: 1.7rem;
			color: $colorLightGray;
		}
	}
}

// e : 우편번호 찾기

//s : 주소 리스트
.address-list {
	margin-top: 10px;

	li {
		position: relative;

		.address-box, .delivery-box {
			width: 100%;
			padding: 14px 15px 13px;
			border: 1px solid $colorLightenGray;
		}
		.btn-address-select {
			&:after {
				position: absolute;
				top: 15px;
				right: 15px;
				content:'select';
				text-transform: uppercase;
				font-size: $bodyText6;
				line-height: 1.4rem;
				text-decoration: underline;
			}
			.delivery-info {
				padding-right: 55px;
			}
		}

		.ip-address-box, .address-box {

			.delivery-info {
				.name, .phone {
					display: inline-block;
					font-size: $bodyText5;
					vertical-align: top;
					line-height: 1.8rem;
				}

				.phone {
					&:before {
						content: '/';
						display: inline-block;
						vertical-align: top;
						margin: 0 3px;
					}
				}

				+ .address-info {
					margin-top: 10px;
				}
			}

			.zip-code {
				font-size: $bodyText5;
				line-height: 1.8rem;

				+ .address-info {
					margin-top: 9px;
				}
			}

			.address-info {
				@include justify_width;
				font-size: $bodyText6;
				line-height: 1.8rem;

				.address-title {
					flex: 0 0 50px;
					padding-right: 10px;
				}

				.address-detail {
					flex: 1;
					font-weight: 300;
				}

				+ .address-info {
					margin-top: 5px;
				}
			}

		}

		.ip-address-box {
			input[type=radio] {
				@include offscreen;

				+ .ip-address-view {
					overflow: hidden;
					position: relative;
					display: block;
					padding: 14px 15px 13px;
					border: 1px solid $colorLightenGray;

					&[disabled], &.disabled {
						color: $colorLightenGray;
						background-color: #fff;
						border-color: $colorLightenGray;
						pointer-events: none;
					}
				}

				&:checked {
					+ .ip-address-view {
						z-index: 2;
						color: $colorBlack;
						border: 1px solid $colorBlack;
						transition: all 0.5s;
					}
				}
			}
		}

		.btn-address-select {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
		}

		.btn-wrap {
			position: absolute;
			top: 11px;
			right: 11px;
			z-index: 2;
			font-size: 0;

			.btn-link {
				padding: 4px;
			}
		}

		~ li {
			margin-top: 10px;
		}

		&.selected {
			.address-box {
				border-color: $colorBlack;
			}
		}

		&.basic {
			.address-box {
				border-color: $colorBlack;

				.delivery-info {
					.name {
						&:before {
							content: '[기본]';
							font-weight: 500;
							padding-right: 3px;
						}
					}
				}
			}
		}
	}

	+ .address-list {
		margin-top: 20px;
		padding-top: 21px;
		border-top: 1px solid $colorLightestGray;
	}
}

.address-type {
	display: block;
	font-size: 1.2rem;
	line-height: 2.5rem;

	~ .address-type {
		margin-top: 25px;
		padding-top: 29px;
		border-top: 1px solid $colorLightenGray;
	}
}

//e : 주소 리스트

.col-input {
	~ .col-input {
		margin-top: 5px;

		.ip-byte-check {
			height: 100px;
		}
	}
}

.btn-flex-wrap {
	@include justify_width;

	.ui-input, .ip-text-wrap {
		flex: 1;
	}

	.flex-text {
		align-self: center;
		font-size: $bodyText5;
		line-height: 1.8rem;
		color: $colorDarkGray;
	}

	.btn {
		flex-shrink: 0;
		min-width: 70px;
		margin-left: 5px;
	}
}

.ui-row-list {
	font-size: 0;

	li {
		display: inline-block;
		vertical-align: top;

		~ li {
			margin-left: 29px;
		}
	}
	$columnCnt: 7; // 컬럼 갯수(2~7)

	@for $i from 1 through $columnCnt {
		&.col-#{$i} {
			@include justify_width(start);
			flex-wrap: wrap;

			li {
				-ms-flex: 0 1 percentage(1 / $i);
				flex: 0 1 percentage(1 / $i);
				width: percentage(1 / $i);
				margin: {
					left: 0;
					top: 2px;
				}

				.ui-rdo {
					margin-left: 5px;
				}

				.ui-rdo-box {
					margin-left: -1px;
				}

				&:nth-child(#{$i}n-1) {
					.ui-rdo {
						margin: {
							left: 0;
							right: 5px;
						}
					}

					.ui-rdo-box {
						margin-left: 0;
					}
				}

				&:nth-child(#{$i}) ~ li {
					margin-top: 7px;
				}
			}
		}
	}
}

.term-agree-wrap {
	border-top: 1px solid $colorBlack;

	.text-list-title {
		margin: 14px 0 11px;

		+ .term-list {
			margin-top: 0;
		}
	}

	.term-list {
		margin-top: 15px;
		border: 1px solid $colorLightenGray;
		border-bottom: none;

		li {
			@include justify_width;
			align-items: center;
			flex-wrap: wrap;
			min-height: 39px;
			padding: 0 10px 0 15px;
			border-bottom: 1px solid $colorLightenGray;

			> .ui-chk {
				flex: 1;
				padding: 9px 5px 9px 0;

				input {
					+ label {
						color: $colorBlack;
					}
				}
			}

			.btn-link {
				padding: 5px;
			}

			.term-sub-box {
				position: relative;
				width: 100%;

				.ui-chk {
					padding: 10px 0 9px;
				}

				&:before {
					content: '';
					position: absolute;
					top: 0;
					left: -15px;
					right: -10px;
					height: 1px;
					background-color: $colorLightenGray;
				}

			}
		}
	}

	&.tp-fold {
		border-top:0;
		.fold-box {
			padding: 0;
			.fold-box-header {
				width: 100%;
			}
		}
	}
}