@import 'vars';
@import 'functions';

// ==== 기본 MIXIN ====

// float하는 상위 DOM에 선언
@mixin clearfix {
	&:after {
		content: '';
		display: block;
		width: 100%;
		height: 0;
		clear: both;
	}
}

// 한줄 말줄임표
@mixin ellipsis($autoWidth: true, $isInline: true, $clearLineHeight: true) {
	// 멀티 라인 값 클리어
	@include clear_multi_ellipsis;

	@if ($isInline) {
		display: inline-block;
	}
	@if ($autoWidth) {
		width: 100%;
	}
	max-width: 100%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	@if ($clearLineHeight) {
		line-height: normal;
	}
}

// n줄 말줄임표
@mixin multi_ellipsis($line-height, $lines: 2, $useHeight: true) {
	overflow: hidden;
	@if $useHeight {
		height: $line-height * $lines;
	}
	max-height: $line-height * $lines;
	line-height: $line-height;

	display: block;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: $lines;
	line-clamp: $lines;

	text-overflow: -o-ellipsis-lastline;

	white-space: normal;
	word-break: break-word;
}

@mixin clear_multi_ellipsis {
	display: inherit;
	height: auto;
	max-height: none;
	-webkit-box-orient: inherit;
	-webkit-line-clamp: inherit;
}

// Display Flex를 활용한 영역대비 사이즈 자동 조절
@mixin justify_width($type: between, $isInline: false, $isReverse: false) {
	display: -webkit-box;
	display: -ms-flexbox;
	@if ($isInline) {
		display: -ms-inline-flexbox;
		display: inline-flex;
		display: -webkit-inline-flex;
	} @else {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		display: -webkit-flex;
	}
	@if ($type == between) {
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	} @else if ($type == space-around) {
		-ms-flex-pack: distribute;
		justify-content: space-around;
		-webkit-justify-content: space-around;
	} @else if ($type == center) {
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		justify-content: center;
		-webkit-justify-content: center;
	} @else if ($type == start) {
		-webkit-box-pack: start;
		-ms-flex-pack: start;
		justify-content: flex-start;
	} @else if ($type == end) {
		-webkit-box-pack: end;
		-ms-flex-pack: end;
		justify-content: flex-end;
	}

	@if $isReverse {
		-webkit-box-orient: horizontal;
		-webkit-box-direction: reverse;
		-ms-flex-direction: row-reverse;
		flex-direction: row-reverse;
	}
}

@mixin calc_letter_spacing($psValue, $fontSize: 1rem) {
	$isPX: is_absolute_length($fontSize);
	@if ($fontSize != 1rem) {
		font-size: $fontSize;
	}
	@if $isPX {
		letter-spacing: ($psValue * $fontSize / 1000);
	} @else {
		letter-spacing: ($psValue * convertRemToPx($fontSize) / 1000);
	}
}

@mixin set_placeholder_color($color: rgba(255, 255, 255, .65)) {
	&::-webkit-input-placeholder {
		color: $color;
	}

	&:-ms-input-placeholder {
		color: $color;
	}

	&::-ms-input-placeholder {
		color: $color;
	}

	&::placeholder {
		color: $color;
	}

	&:-ms-input-placeholder {
		color: $color;
	}

	&::-ms-input-placeholder { /* Microsoft Edge */
		color: $color;
	}
}

@mixin hidden {
	width: 0;
	height: 0;
	margin: 0;
	padding: 0;
	font-size: 0;
	line-height: 0;
	overflow: hidden;
}

@mixin no_select_text {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

@mixin vertical_middle($target: before) {
	&:#{$target} {
		content: '';
		display: inline-block;
		height: 100%;
		vertical-align: middle;
	}
}

// ==== 프로젝트 MIXIN ====

// mobile background
@mixin background_img($url, $imgWidth, $imgHeight) {
	display: block;
	background: {
		image: url($url);
		repeat: no-repeat;
		position: 0 0;
		size: ($imgWidth/2) ($imgHeight/2);
	}
}

// 모바일 small
@mixin mob_small {
	@media screen and (max-width: #{$mobSmallWidth}) {
		@content;
	}
}

// 모바일 가로모드
@mixin device_horizontal {
	/* 가로모드 */
	@media all and (orientation: landscape) {
		@content;
	}
}

// 모바일 디자인 기준 큰 화면
@mixin mob_middle {
	/* 가로모드 */
	@media screen and (min-width: #{$mobMiddleWidth}) {
		@content;
	}
}

// 텍스트 숨김
@mixin offscreen {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	border: 0;
	font-size: $bodyText6;
}