@import 'scss_header';

.fold-box-list {
	.fold-box {
		.fold-box-header {
			position: relative;
			font-size: 1.5rem;

			.toggle-title-box {
				/*border-color: $colorLightenGray !important;*/

				.toggle-title {
					flex: 1;
				}

				.ui-chk, .ui-rdo {
					padding: 10px 0 9px;

					input {
						+ label {
							color: $colorBlack;
						}
					}
				}
			}

			.btn-link {
				vertical-align: middle;
				@include vertical_middle;
			}

			.right-wrap {
				font-size: 0;
			}

			&.header-only {
				.toggle-title-box {
					.ico-toggle {
						display: none;
					}
				}
				~ .fold-box-contents {
					display: none;
				}
			}
		}

		.fold-box-contents {
			overflow: hidden;
			height: 0;
			padding: 0 15px;
			word-break: break-all;

			.editor-wrap {

				+ .attach-file-list {
					margin-top: 15px;
				}
			}

			.attach-file-list {
				.attach-title {
					display: block;
					margin-bottom: 10px;
					font-size: $bodyText5;
					letter-spacing: -0.03rem;
					color: $colorLightGray;
					line-height: 1.4rem;
				}

				.file-list {
					li {
						a {
							display: inline-block;
							font-size: $bodyText6;
							color: $colorDarkGray;
							line-height: 1.8rem;
							vertical-align: top;
							text-decoration: underline;
							letter-spacing: -0.028rem;
						}

						~ li {
							margin-top: 8px;
						}
					}
				}
			}

			.guide-info-wrap {
				margin-top: 0;

				&:first-of-type {
					margin-top: 5px;
				}
			}
		}

		&.expanded {
			.fold-box-contents {
				border-bottom: 1px solid $colorLightestGray;
				height: auto;
				padding: {
					top: 15px;
					bottom: 20px;
				}
			}
		}
	}

	&.fold-box-lg {
		border-top: 1px solid $colorBlack;

		.fold-box-header {
			.toggle-title-box {
				min-height: 45px;
			}
		}
	}
}

// s : 약관 폴딩 박스
.term-fold-wrap {

	.text-list-title {
		margin: 10px 0 8px;
		line-height: 1.8rem;
	}

	.fold-box-list {
		border: 1px solid $colorLightenGray;
		border-bottom: none;

		.fold-box {

			.fold-box-header {
				.toggle-title-box {
					border-bottom-color: #ccc;
					.toggle-title {
						font-size: $bodyText4;
					}
				}
			}

			.fold-box-contents {
				overflow-y: auto;
				max-height: 147px;
			}

			&.expanded {
				.fold-box-contents {
					border-bottom-color: #ccc;
					padding-bottom: 15px;
				}
			}
		}
	}
}


// e : 약관 폴딩 박스

// s : 공지사항 폴딩 박스
.tab-wrap {
	.tab-content {
		.notice-fold-wrap {
			.fold-box-list {
				margin-top: 20px;
			}
		}
	}
}

.notice-fold-wrap, .faq-fold-wrap {
	.fold-box-list {

		.fold-box {
			.fold-box-header {
				.toggle-title-box {
					border-color: $colorLightestGray !important;

					.toggle-title {
						margin-right: 15px;
					}

					.ico-toggle {
						width: 9px;
						height: 9px;
						@include background_img('../images/ui/button/ui_btn_prod_control.png', 36px, 18px);
						background-position: -9px 0;

						&.is-reverse {
							background-position: 0 0;
						}
					}
				}
			}

			&.expanded {
				.fold-box-contents {
					border-color: $colorLightestGray !important;
				}
			}
		}
	}

	+ .btn-more {
		margin-bottom: 0;
	}
}

.notice-fold-wrap {
	.fold-box-list {
		border-top: 1px solid $colorBlack;

		.fold-box {
			.fold-box-header {
				padding-left: 0;

				&.notice-fixed {
					background: #f9f9f9;
				}

				.toggle-title-box {
					padding: 11px 15px 9px 0;

					.notice-date {
						font-family: $fontEng;
					}

					.notice-type, .notice-date {
						vertical-align: middle;
						text-align: center;
						text-transform: uppercase;
						font-size: 1.4rem;
						@include vertical_middle;
					}

					.notice-type {
						flex: 0 0 52px;
						font-size: 1rem;
						color: $colorDarkGray;
					}

					.toggle-title {
						font-size: $bodyText5;
						line-height: 1.6rem;
						color: $colorDarkGray;
					}

					.notice-date {
						display: block;
						margin-top: 2px;
						color: $colorLightGray;
						font-size: 1rem;
						text-align: left;
					}
				}
			}

			&.expanded {
				.fold-box-contents {
					padding: 15px 0;

					.editor-wrap {
						font-size: $bodyText5;
						line-height: 1.8rem;
						letter-spacing: -0.03rem;
						color: $colorDarkGray;

						img {
							max-width: 100% !important;
						}

						p {
							img {
								max-width: 100% !important;
							}
						}
					}
				}
			}
		}
	}
}

// e : 공지사항 폴딩 박스

// s : FAQ 폴딩 박스
.faq-fold-wrap {
	.fold-box-list {
		border-top: 1px solid $colorBlack;

		.fold-box {
			.fold-box-header {
				.toggle-title-box {
					padding: 10px 15px 9px 0;

					.faq-rank {
						flex: 0 0 43px;
						font-weight: 500;
						font-size: $bodyText5;
						line-height: 22px;
						text-align: center;
						color: $colorDarkGray;

						&:before {
							content: 'Q';
						}

						+ .toggle-title {
							padding-left: 0;

							&:before {
								content: none;
							}
						}
					}

					.faq-type {
						flex: 0 1 60px;
						font-size: 1rem;
						color: $colorDarkGray;
					}

					.toggle-title {
						position: relative;
						padding-left: 16px;
						font-size: $bodyText5;
						letter-spacing: -0.06rem;
						line-height: 1.6rem;
						color: $colorDarkGray;

						&:before {
							position: absolute;
							content: 'Q';
							font-family: $fontEng;
							font-weight: 500;
							left: 0;
							top: -2px;
						}
					}
				}
			}

			&.expanded {
				.fold-box-contents {
					padding: 15px 0;

					.editor-wrap {
						font-size: $bodyText5;
						line-height: 1.8rem;
						letter-spacing: -0.03rem;
						color: $colorDarkGray;

						img {
							max-width: 100% !important;
						}

						p {
							img {
								max-width: 100% !important;
							}
						}
					}
				}
			}
		}
	}
}

// e : FAQ 폴딩 박스

// s : 리뷰 폴딩 박스
.review-fold-wrap {
	.fold-box-list {
		.fold-box {
			.fold-box-header {
				border-bottom: none;
				padding: 0;

				.review-header {
				}
			}

			&.expanded {
				.fold-box-header {
					.review-info-wrap {
						.review-desc {
							@include clear_multi_ellipsis;

							p {
								display: block;
							}

							.review-desc-img {
								display: block;
								margin-top: 15px;
							}
						}
					}
				}
			}
		}
	}
}

// e : 리뷰 폴딩 박스

// s : Q&A 폴딩 박스
.qna-fold-wrap {
	.fold-box-list {
		.fold-box {
			border-bottom: solid 1px $colorLightestGray;
			padding: 15px 0;

			.fold-box-header {
				padding: 0;

				.qna-header {
					display: block;
					font-size: 0;

					.qna-header-title {
						@include justify_width;
						align-items: center;

						.question-state, .question-type, .nick-name, .question-date {
							display: inline-block;
							color: $colorLightGray;
							font-size: $bodyText6;
							line-height: 1.7rem;
						}

						.question-state {
							&.complete {
								color: $colorBlack;
							}
						}

						.question-type {
							margin-left: 10px;
						}

						.nick-name {
							color: $colorBlack;
							font-weight: 700;
						}

						.question-date {
							font-size: 1rem;
							color: $colorLightGray;
							margin-left: 6px;
							text-transform: uppercase;
						}
					} 

					.qna-question {
						margin-top: 6px;

						.qna-question-text {
							font-size: $bodyText5;
							line-height: 2rem;
							color: $colorDarkGray;
							white-space: pre-line;
						}

						&.qna-secret {
							.icon-secret {
								width: 20px;
								height: 20px;
								margin-left: 2px;
								margin-top: -1px;
								@include background_img('../images/common/comm_icon_secret.png', 40px, 40px);
								vertical-align: top;
								display: inline-block;
							}
						}
					}
				}
			}

			.fold-box-contents {
				padding: 0;
				border-bottom: 0;

				.qna-contents-title, .qna-answer {
					font-size: $bodyText5;
				}

				.qna-answer {
					margin-top: 5px;
					line-height: 2rem;
					color: $colorDarkGray;
				}
			}

			&:not(.expanded) {
				.fold-box-header {
					.qna-header {
						.qna-question {
							.qna-question-text {
								@include ellipsis(false, false, false);
								display: inline-block;
								vertical-align: top;
							}

							&.qna-secret {
								.qna-question-text {
									max-width: calc(100% - 22px);
								}
							}
						}
					}
				}
			}

			&.expanded {
				.fold-box-header {
					border-bottom: none;

					.qna-header {
						padding-bottom: 15px;
					}
				}
			}
		}
	}
}

// e : Q&A 폴딩 박스