@import 'scss_header';


// s: 페이지 타이틀
.ptitle {
	font-size: 1.5rem;
	font-weight: 300;
	text-align: center;
	color: $colorBlack;
	text-transform: uppercase;
}

// e: 페이지 타이틀

// s: sub 타이틀
.stitle {
	display: block;
	padding-top: 14px;
	margin-bottom: 15px;
	font-size: 1.8rem;
	font-weight: 500;
	line-height: 2.3rem;
	color: $colorBlack;
}

.stitle-wrap {
	@include justify_width;
	align-items: center;

	.stitle {
		padding-top: 0;
		margin-bottom: 0;
	}

	.right-wrap {
		margin-left: auto;

		.btn-link {
			font-size: 1.3rem;
		}
	}
}

// e: sub 타이틀

// s: section 타이틀
.wrap {
	.contents-wrap {
		.cont-padding {
			> .sctitle, > .sctitle-wrap {
				&:first-child {
					margin-top: 15px;
				}
			}

			> .sctitle-wrap {
				.sctitle:first-child {
					margin-top: 0;
				}
			}
		}
	}
}

.ui-dialog {
	.ui-dialog-contents {
		.dialog-contents {
			.sctitle, .sctitle-wrap {
				&:first-child {
					margin-top: 0;
				}
			}
		}
	}
}

.sctitle {
	margin: 40px 0 9px;
	font-family: $fontMix;
	font-size: $bodyText4;
	line-height: 1.9rem;

	+ .tbl-form {
		padding-top: 15px;
		border-top: 1px solid $colorBlack;
	}
}

.sctitle-wrap {
	margin: 40px 0 9px;
	@include justify_width;
	align-items: center;

	.sctitle {
		display: inline;
		margin: 0 !important;

		&:first-child {
			margin: 0;
		}

		+ .right-wrap {
			align-self: flex-end;
		}
	}

	.ui-chk {
		margin: -2px 0;
	}

	.right-wrap {
		flex-shrink: 0;

		.btn {
			min-width: 80px;
			margin-bottom: -19px;
			margin-top: 3px;

			+ .btn {
				margin-left: 4px;
			}
		}

		.btn-link {
			display: inline-block;

			+ .btn-link {
				margin-left: 10px;
			}
		}

		.desc-gray {
			display: inline-block;
		}

	}

	+ .tbl-form {
		padding-top: 9px;
		border-top: 1px solid $colorBlack;
	}

	+ .term-fold-wrap {
		&:before {
			content: '';
			display: block;
			margin-bottom: 10px;
			border-top: 1px solid $colorBlack;
		}
	}
}

// e: section 타이틀

//s : title 공통
.toggle-title-box {
	position: relative;
	@include justify_width(start);
	padding: 0 15px 0 15px;
	align-items: center;
	min-height: 40px;
	border-bottom: 1px solid $colorLightestGray;

	.toggle-title {
		font-size: $bodyText3;
		color: $colorBlack;
		margin-right: 10px;
	}

	.right-box {
		margin-left: auto;

		.ico-toggle {
			width: 20px;
			height: 20px;
			@include background_img('../images/ui/button/ui_btn_toggle.png', 80px, 40px);
			background-position: -20px 0;
			display: inline-block;
			vertical-align: top;

			&.is-reverse {
				background-position: 0 0;
			}
		}
	}
}

