@import 'scss_header';

/* s: box-list-wrap */
.box-list-wrap {
	.box-item {
		padding: 15px 0;

		~ .box-item {
			border-top: 1px solid $colorLightenGray;
		}

		.event-box {
			padding-bottom: 15px;
		}

	}

	&.event-type {
		.box-item {
			@include justify_width;

			.event-item-box {
				flex: 1;
				overflow: hidden;
			}

			&:nth-child(n+4) {
				.event-item-box {
					flex: 0 1 50%;

					&:nth-child(2n - 1) {
						padding-right: 5px;
					}

					&:nth-child(2n) {
						padding-left: 5px;
					}
				}
			}

			&:last-child {
				padding-bottom: 0;

				.event-item-box {
					padding-bottom: 0;
				}
			}
		}

		.no-data {
			&:not(.full) {
				padding-bottom: 50px;
			}
		}
	}

	&.brand-type {
		.box-item {
			~ .box-item {
				padding-top: 19px;
				border-top: 0;
			}
		}
	}
}

// s: event-box
.event-item-box {
	font-family: $fontMix;
	@include calc_letter_spacing(-50, $bodyText2);
	padding-bottom: 16px;

	.box-link {
		display: block;
	}

	.ratio-img-wrap .img-viewer {
		padding-top: 66.6666%;
	}

	.event-title {
		display: block;
		margin-top: 15px;
		font-size: $bodyText2;
		font-weight: 500;
		line-height: 2.2rem;
		letter-spacing: -0.038rem;
		@include ellipsis;
	}

	.event-desc {
		display: block;
		margin-top: 10px;
		font-size: $bodyText4;
		letter-spacing: -0.033rem;
		text-align: left;
		color: $colorDarkGray;
		@include multi_ellipsis(2.2rem);
	}

	.event-period {
		display: block;
		margin-top: 11px;
		font-size: 1rem;
		font-weight: 300;
		line-height: 1em;
		color: $colorLightGray;
	}
}

// e: event-box

// s: archive-box
.archive-item-box {
	font-family: $fontMix;
	@include calc_letter_spacing(-50, $bodyText2);
	padding-bottom: 14px;

	.box-link {
		display: block;
	}

	.ratio-img-wrap .img-viewer {
		padding-top: 66.6666%;
	}

	.archive-title {
		display: block;
		margin-top: 15px;
		font-size: $bodyText2;
		font-weight: 500;
		line-height: 2.2rem;
		letter-spacing: -0.038rem;
		@include ellipsis;
	}

	.archive-desc {
		display: block;
		margin-top: 8px;
		font-size: $bodyText4;
		letter-spacing: -0.033rem;
		text-align: left;
		color: $colorDarkGray;
