// e: archive-box

// s: content-box
.contents-item-box {
	font-family: $fontMix;
	@include calc_letter_spacing(-50, $bodyText2);
	padding-bottom: 14px;

	.box-link {
		display: block;
	}

	.ratio-img-wrap {
		text-align: center;
		img {
			max-width: 100%;
		}
		.img-viewer {
			padding-top: 66.6666%;
		}

	}

	.contents-title {
		display: block;
		margin-top: 15px;
		font-size: $bodyText2;
		font-weight: 500;
		line-height: 2.2rem;
		letter-spacing: -0.038rem;
		@include ellipsis;
	}

	.contents-desc {
		display: block;
		margin-top: 8px;
		font-size: $bodyText4;
		letter-spacing: -0.033rem;
		text-align: left;
		color: $colorDarkGray;
		@include multi_ellipsis(2rem);
	}

	.profile-flex-wrap {
		padding: 10px 0;
		border-bottom: 1px solid $colorLightestGray;
	}
}

// e: archive-box

// s: insider-box
.insider-item-box {
	position: relative;

	.insider-profile {
		position: absolute;
		bottom: 40px;
		left: 0;

		.ratio-img-wrap {
			overflow: inherit;
			width: 80px;
			height: 80px;
			border-radius: 80px;
			background-color: #fff;
			border: 1px solid $colorLightenGray;
			font-size: 5rem;
			font-weight: 300;
			line-height: 7.8rem;
			text-align: center;

			.img-viewer {
				overflow: hidden;
				width: 80px;
				height: 80px;
				border-radius: 80px;
				margin: -1px;
				padding-top: 100%;
			}
		}
	}

	.insider-content-list-wrap {
		padding-left: 60px;

		.insider-content-list {
			@include justify_width;

			.insider-item {
				overflow: hidden;
				align-self: flex-end;
				width: 33.33%;

				.insider-content {
					display: block;
					@include justify_width;
					max-height: 135px;

					img {
						flex: 1;
						align-self: flex-end;
						max-width: 100%;
					}
				}
			}
		}
	}

	.box-footer {
		margin-top: 10px;
		@include justify_width;

		.insider-info {
			flex: 1;
			display: block;
			overflow: hidden;

			.insider-title {
				margin-right: auto;
				@include ellipsis;
				font-size: $bodyText5;
				font-weight: 700;
				color: $colorBlack;
				display: block;
				line-height: 1.17;
				vertical-align: top;
				text-transform: uppercase;
			}

			.insider-post-count {
				@include ellipsis;
				margin-top: 3px;
				display: block;
				font-size: 1rem;
				font-weight: 300;
				line-height: 1.3rem;
				color: $colorLightGray;
			}

		}

		.insider-like-count {
			align-self: center;
			padding: {
				left: 5px;
				right: 5px;
			}
			font-size: $bodyText5;
			line-height: 1.5rem;
			letter-spacing: -0.03rem;
		}

		.btn-item-equal {
			width: 30px;
			height: 30px;
			padding: 0;
		}
	}

}

// e: insider-box

// s: thumb-box
.thumb-item-box {
	.ratio-img-wrap {
		position: relative;

		.img-viewer {
			padding-top: 100%;
		}

		.img-text-wrap {
			z-index: 1;
			position: absolute;
			bottom: 15px;
			left: 15px;
			right: 15px;

			.editor-wrap {
				@include ellipsis;
				display: block;
				height: 30px;
				padding: 0 10px;
				background: rgba(255, 255, 255, 0.8);
				text-align: center;
				line-height: 3rem;
				font-size: $bodyText3;

				~ .editor-wrap {
					margin-top: 5px;
				}
			}
		}
	}
}

// e: thumb-box

// s: exclusive-box
.exclusive-item-box {
	position: relative;
	border-bottom: 1px solid $colorBlack;

	.box-footer {
		@include justify_width(center);
		border-top: 1px solid $colorBlack;
		text-align: center;
		padding: 12px 10px;
		align-items: center;
		display: block;
		@include ellipsis;
		font-size: 1.3rem;

		.plan-title { 
			position: relative;
			padding-left: 10px;
			flex: 0 1 auto;

			&:after {
				content: '/';
				position: absolute;
				width: 10px;
				text-align: center;
				top: 50%;
				left: 0;
				transform: translateY(-50%);
			}
		}
	}
}

// e: exclusive-box

// s: brand-box
.brand-item-box {
	.profile-flex-wrap {
		padding: 0 15px;
		margin-bottom: 10px;
	}
}

// e: brand-box

/* s: box-list-wrap */

/* s: flex-box-list-wrap */
.flex-box-list-wrap {
	$columnCnt: 5; // 컬럼 갯수(2~5)
	@include justify_width(start);
	flex-wrap: wrap;

	.flex-box-item {

		.box-link {
			display: block;
		}

		.thumb-item-box {
			padding: {
				right: 5px;
				top: 5px;
				bottom: 5px;
			}
		}

		.exclusive-item-box {
			&:after {
				z-index: 2;
				content: '';
				position: absolute;
				top: 0;
				bottom: 0;
				right: 0;
				display: block;
				width: 1px;
				background: $colorBlack;

			}
		}

		&:nth-child(2n) {
			.thumb-item-box {
				padding: {
					right: 0;
					left: 5px;
				}
			}

			.exclusive-item-box {
				&:after {
					display: none;
				}
			}
		}
	}

	&.exclusive-type {
	}

	@for $i from 1 through $columnCnt {
		&.col-#{$i} {

			.flex-box-item {
				-ms-flex: 0 1 percentage(1 / $i);
				flex: 0 1 percentage(1 / $i);
				width: percentage(1 / $i);
			}
		}
	}


}

/* e: flex-box-list-wrap */

.attach-loading {
	position: relative;
	height: 40px;
}