@import 'scss_header';

.ui-dialog {
	&:not(.dialog-bottom) {
		&:before {
			opacity: 0;
			content: '';
			display: block;
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: rgba(255, 255, 255, 0.6);
		}
	}
	&.dialog-alert, &.dialog-notice {
		width: $modalPopupSizeAlert !important;
		margin: {
			left: auto;
			right: auto;
		}

		.ui-dialog-titlebar {
			padding: 30px 15px 0;

			.ui-dialog-title {
				text-align: center;

				.spot {
					font-weight: 500;
				}
			}
		}

		.ui-dialog-contents {
			//padding-top: 20px;
			word-break: keep-all;
			word-wrap: break-word;

			.dialog-alert-text {
				padding: 0 15px;
				color: $colorBlack;
				text-align: center;
				font-size: $bodyText5;
				font-weight: 300;
				line-height: 2rem;

				.spot {
					font-weight: 400;
				}
			}

			.dialog-notice-text {
				padding: 0 15px;
				margin-top: 9px;
				font-weight: 300;
				color: $colorBlack;
				text-align: center;
				font-size: $bodyText5;
				line-height: 2rem;

				.spot {
					font-weight: 400;
				}
			}

			.dialog-sub-text {
				padding: 0 15px;
				margin-top: 9px;
				font-size: 1.1rem;
				line-height: 2rem;
				color: $colorLightGray;
				text-align: center;
				font-weight: 300;

				.spot {
					color: $colorBlack;
					font-weight: 300;
				}

				~ .dialog-notice-text {
					margin-top: 19px;
				}
			}

			.dialog-text-wrap {
				padding-bottom: 30px;
			}

			.btn-even-wrap {
				margin-top: 0;
				border-top: 1px solid $colorLightestGray;
			}
		}
	}

	&.dialog-alert {

		.ui-dialog-titlebar {
			position: relative;

			.ui-dialog-title {
				position: absolute;
				width: 1px;
				height: 1px;
				padding: 0;
				margin: -1px;
				overflow: hidden;
				clip: rect(0, 0, 0, 0);
				border: 0;
				font-size: $bodyText6;
			}
		}

	}

	&.dialog-bottom {
		max-height: calc(100vh - #{$headerHeightDefault} - 1px ) !important;
		height: auto;
		align-self: flex-end;
		transform: translateY(100%) scale(1);
		-wekbit-transform: translateY(100%) scale(1);
		-webkit-transition: transform $transDefaultTime;
		transition: transform $transDefaultTime;
		-webkit-backface-visibility: hidden;
		border-width: 1px 0 0;

		.ui-dialog-titlebar {
			display: none;

			.ui-dialog-title {
				font-size: 0;
			}
		}

		.ui-dialog-contents {
			overflow: hidden;
			max-height: calc(100vh - #{$headerHeightDefault}) !important;
			@include justify_width;
			flex-direction: column;
			height: 100%;
			/*
			transition: transform 0.5s 0.5s;*/

			&#prdoption {
				.btn-even-wrap {
					.btn {
						font-size: $bodyText3;
					}
				}
			}

			.toggle-title-box {
				flex-shrink: 0;
				border-color: $colorLightestGray;

				.toggle-title {
					font-size: $bodyText6;
					text-transform: uppercase;
				}

				.btn-pop-close {
					position: relative;
					display: block;

					.count {
						display: inline-block;
						font-size: $bodyText6;
						font-family: $fontEng;
						padding: 3px 5px 0 0;
					}
				}
			}

			.dialog-contents {
				overflow-y: auto;
				-webkit-overflow-scrolling:touch;
				flex: 1;
				height: calc(100% - #{$headerHeightDefault});
				max-height: calc(100vh - #{$headerHeightDefault + 40});

				.control-list-wrap {
					position: relative;
					@include justify_width;
					justify-content: flex-end;
					-webkit-justify-content: flex-end;
					padding: 6px 0;

					.category-list {
						width: calc(100% - 100px);

						.category-item {
							font-size: 1.6rem;
							color: $colorBlack;
							border-top: solid 1px #fff;

							~ .category-item {
								border-color: $colorLightestGray;
							}

							.link-site,
							.btn-sort-value {
								display: block;
								width: 100%;
								line-height: 39px;
								text-transform: uppercase;
								text-align: left;
							}
						}

					}
				}

				.filter-wrap {
					height: calc(100vh - 40px - 45px - 65px);
					@include justify_width;
					flex-direction: column;

					.filter-tab {
						overflow: hidden;
						position: relative;
						@include justify_width;
						flex: 1;
						max-height: 100%;
						margin: 0 -15px;
						padding: 9px 15px 0;

						.tabs {
							display: block;
							width: 85px;
							height: auto;
							padding: 0;
							margin-right: 15px;
							margin-bottom: 0;
							border: none;
							transform: none;
							-webkit-transform: none;
							-moz-transform: none;
							-ms-transform: none;

							li {
								float: none;
								width: 100%;
								height: 40px;
								margin: 0;
								border: none;
								line-height: 40px;

								a, .ui-tabs-anchor {
									display: block;
									height: 100%;
									color: $colorGray;
									padding: 0;
									line-height: 40px;
									font-size: $bodyText5;
									text-align: left;

									.cnt {
										display: inline-block;
									}
								}

								&.ui-tabs-active {

									a, .ui-tabs-anchor {
										color: $colorBlack;
										font-weight: 400;
									}
								}
							}
						}

						.tab-content {
							overflow-y: auto;
							-webkit-overflow-scrolling:touch;
							width: calc(100% - 100px);

							.filter-title {
								display: block;
								font-size: 1.6rem;
								border-bottom: 1px solid $colorLightestGray;
								line-height: 39px;
							}

							&.filter-category {
								.ui-col-list {
									li {
										.ui-rdo, .ui-chk {
											min-height: 39px;
											display: flex;
											align-items: center;
											padding: 0;
										}
									}

									> li {
										> .ui-rdo, > .ui-chk {
											position: relative;
											width: 100%;
											min-height: 40px;
											padding-left: 3px;

											&:before {
												content: '';
												position: absolute;
												bottom: 0;
												left: 0;
												right: 0;
												height: 1px;
												background-color: $colorLightestGray;
											}
										}
									}

									&.fold-box-list {
										border: none;

										.fold-box {
											border: none;

											.fold-box-header {
												padding: 0;
												padding-left: 33px;
												border-bottom: 1px solid $colorLightestGray;
											}

											.fold-box-contents {
												padding: 0;
												border-bottom: none;

												ul {
													li {
														padding-left: 63px;
														border-bottom: 1px solid $colorLightestGray;
													}
												}
											}

											&.expanded {
												.fold-box-header {
													border-color: $colorLightestGray;
												}
											}
										}
									}
								}
							}

							&.filter-brand {
								overflow-y: visible;
								position: relative;

								.brand-list-wrap {
									overflow-y: auto;
									-webkit-overflow-scrolling:touch;
									height: 100%;
									margin-top: 0;

									.ui-col-list {
										li {
											.ui-chk, .ui-rdo {
												input {
													+ label {
														width: 100%;

														&::before {
															top: 4px;
														}

														&::after {
															top: 5px;
														}

														.brand-name {
															@include ellipsis;
															width: auto;
															max-width: calc(100% - 30px);
															padding-right: 5px;
															height: $bodyText4;
															max-height: $bodyText4;
															margin-top: 2px;
															vertical-align: middle;
															line-height: $bodyText4;
														}

														.cnt {
															display: inline-block;
															vertical-align: middle;
															width: 30px;
														}
													}
												}
											}
										}
									}
								}

								.anchor-list-wrap {
									right: -15px;
								}
							}
						}


						&.control-list-wrap {
							&:before {
								content: none !important;
							}
						}
					}

					.selected-filter-wrap { // 선택한 필터
						margin-top: 15px;
						margin-right: -15px;
						padding-right: 15px;

						.swiper-slide {
							width: 100px;
						}

						.filter-tag-wrap {

							.filter-tag {
								display: inline-block;
								border: 1px solid $colorLightenGray;
								background-color: #fff;
								border-right: none;
								width: auto;
								height: 30px;

								.btn-remove-filter {
									padding: 0 9px 0 10px;

									.filter-name {
										font-size: 1.2rem;
										line-height: 28px;
										font-family: $fontMix;
									}

									&:after {
										content: '';
										width: 10px;
										height: 10px;
										margin: 9px 0 0 4px;
										@include background_img('../images/common/comm_filter_btn_remove.png', 20px, 20px);
										display: inline-block;
										vertical-align: top;
									}
								}

								&:last-child {
									border-right: 1px solid $colorLightenGray;
								}
							}

						}
					}
				}

				.btn-even-wrap {
					margin: {
						top: 10px;
						bottom: 15px;
					}
				}

				.sns-share-wrap {
					@include justify_width;
					align-items: center;
					justify-content: center;
					height: 110px;

					.btn-sns-share {
						flex-shrink: 0;

						~ .btn-sns-share {
							margin-left: 20px;
						}
					}
				}

				.download-coupon-wrap {
					padding: 15px 15px 30px;

					.coupon-visual-wrap {
						text-align: center;

						+ .download-coupon-title {
							margin-top: 20px;
						}
					}

					.download-coupon-title {
						text-align: center;
						font-size: 1.3rem;
					}

					.download-coupon-desc {
						margin-top: 10px;
						text-align: center;
						font-size: 1.2rem;
						font-weight: 300;
						line-height: 2rem;
					}

					.btn-even-wrap {
						margin: {
							top: 30px;
							bottom: 0;
						}
					}
				}
			}
		}

		&.is-open {
			transform: translateY(0) scale(1);;
			-wekbit-transform: translateY(0) scale(1);;
		}

	}
	&.dialog-full {
		.ui-dialog-contents {
			.dialog-contents {
				height: calc(100% - #{$headerHeightDefault}) !important;
			}
		}
	}
	&.dialog-full,
	&.dialog-full-contentdetail {
		margin-top: 10px;
		height: calc(100% - 10px) !important;
		border: 0;
		transform: translateY(100%);
		-webkit-transition: transform #{$transDefaultTime/2};
		transition: transform #{$transDefaultTime/2};

		&:before {
			opacity: 0;
			content: '';
			display: block;
			position: absolute;
			top: -10px;
			left: 0;
			width: 100vw;
			height: 100%;
			background: rgba(255, 255, 255, 0.6);
		}

		.ui-dialog-titlebar {
			.ui-dialog-title {
				display: none;

			}

			.ui-dialog-titlebar-close {
				z-index: 1;
				width: 30px;
				height: 30px;
				@include background_img('../images/ui/button/ui_btn_full_popup_close.png', 60px, 60px);
				right: initial;
				left: 10px;
				top: 8px;
			}
		}

		.ui-dialog-contents {
			overflow: hidden;
			height: 100% !important;

			.dialog-title {
				height: 45px;
				padding: 10px 40px 10px;
				border: {
					top: 1px solid $colorBlack;
					bottom: 1px solid $colorLightestGray;
				}

				.pop-title {
					padding-top: 1px;
					font-size: 1.5rem;
					line-height: 1.9rem;
					text-align: center;
					font-weight: 300;
					color: $colorBlack;
					@include ellipsis;
				}
			}

			.dialog-contents {
				overflow-y: auto;
				-webkit-overflow-scrolling:touch;
				//padding-bottom: 10px;

				.dialog-contents-flex {
					@include justify_width;
					flex-direction: column;
					flex: 1;
					min-height: 100%;

					&.fixed {
						height: 100%;

						.btn-even-wrap {
							margin-top: 0;
							padding-top: 0;
						}
					}

					.btn-even-wrap {
						padding-bottom: 15px;
					}

					.tbl-form {
						padding-top: 15px;
					}

					+ .btn-even-wrap {
						margin-top: 0;
					}
				}

				.terms-wrap {
					padding-top: 15px;
				}
			}
		}

		&.is-open {
			transform: translateY(0);
		}
	}

	.option-select-box {
		margin: {
			top: 15px;
		}
		padding: {
			bottom: 5px;
		}
		max-height: calc(100% - #{$headerHeightDefault} - 51px - 65px - 15px);
		overflow-y: auto;
		-webkit-overflow-scrolling:touch;

		.prd-name {
			margin-bottom: 15px;
			font-size: $bodyText4;
			line-height: 1.9rem;
		}

		.option-select {
			overflow: hidden;
			border: 1px solid $colorLightenGray;

			.fold-box {
				&.expanded {
					.fold-box-contents {
						max-height: 125px;
						padding: {
							top: 7px;
							bottom: 7px;
						}
						overflow-y: auto;
						-webkit-overflow-scrolling:touch;
					}
				}
			}

			.option-label-list {
				.btn-opt {
					display: block;
					width: 100%;
					text-align: left;
					padding: 5px 0;
					font-size: 0;

					.txt {
						font-size: 1.3rem;
						line-height: 2rem;
						letter-spacing: -0.033rem;
					}

					&:disabled {
						color: $colorGray;

						&:after {
							content: 'Sold Out';
							display: inline-block;
							margin-left: 10px;
							font-size: 1.1rem;
							line-height: 1.4rem;
							text-transform: uppercase;
						}
					}

					.lmminent {
						display: block;
						color: $colorGray;

						.lmminent-count {
							margin-left: 10px;
							font-size: 1.1rem;
							line-height: 1.4rem;

							&:before {
								content: '품절임박';
								display: inline-block;
							}
						}
					}
				}
			}

			.fold-box:last-child {
				margin-bottom: -1px;
			}
		}

		.option-result {
			padding: {
				top: 10px;
				bottom: 4px;
			}

			.option-item {
				@include justify_width;
				align-items: center;
				padding: 10px 0;

				.prod-name {
					flex: 1;
					font-size: 1.2rem;
					line-height: 1.8rem;
					color: $colorDarkGray;
				}

				.prod-count {
					margin: {
						left: 20px;
						right: 20px;
					}
					@include mob_small {
						margin: {
							left: 10px;
							right: 10px;
						}
					}

					.ui-spinner {
						.ui-spinner-input {
							width: 34px;
						}
					}
				}
			}
		}

	}

	.desc-ord-black {
		margin-top: 5px;
		font-size: $bodyText6;
		line-height: 1.7rem;
		color: $colorDarkGray;
	}

	.total-cost-box {
		@include justify_width;
		padding: {
			top: 20px;
			bottom: 10px;
		}
		border-top: 1px solid $colorBlack;
		align-items: center;

		.sum-text {
			font-size: 1.2rem;
			line-height: 1.8rem;
			color: $colorDarkGray;
		}

		.sum-value {
			flex: 1;
			padding-left: 15px;
			font-size: 2rem;
			font-weight: 500;
			line-height: 2.5rem;
			color: $colorDarkGray;
			text-align: right;
		}

		~ .btn-even-wrap {
			/*margin-top: 10px;*/
		}
	}

	// 자동완성
	&.dialog-full-autocomplete {
		height: 100% !important;
		border: 0;
		color: $colorBlack;

		.ui-dialog-title,
		.ui-dialog-titlebar-close {
			display: none;
		}

		.ui-dialog-contents {
			padding: 0 15px;
		}

		.autocomplete-title {
			padding-top: 20px;
			font-size: 1.2rem;
			line-height: 1.8rem;
			border-top: 1px solid $colorLightestGray;
		}

		.hsearch-autocomplete-list {
			padding: 10px 0;

			.btn-word {
				padding: 10px 0;
				font-size: 1.3rem;
				font-weight: 300;
				line-height: 1.9rem;

				.fc-spot {
					font-weight: 400;
				}
			}
		}
	}

	$detailBottomPadding: 98px;
	// 상세 스크롤 고정
	.detail-fix-img-wrap {
		padding-top: calc(100vh - #{ $headerHeightDetail + $detailBottomPadding });
		background-color: $colorBlack;

		.fixed-img-wrap {
			position: fixed;
			left: 0;
			right: 0;
			// top: $headerHeightDetail;
			top: calc(#{$headerHeightDetail} + constant(safe-area-inset-top));
			top: calc(#{$headerHeightDetail} + env(safe-area-inset-top));
			
			height: calc(100vh - #{ $headerHeightDetail + $detailBottomPadding });
			z-index: 1;

			.bg {
				z-index: 5;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				height: 100%;
				display: block;
				background: #fff;
				opacity: 0;
				pointer-events: none;
			}

			.lookbook-banner-wrap {
				height: 100%;
				z-index: 1;

				.swiper-wrapper {
					.swiper-slide {
						.btn-content-detail {
							position: absolute;
							right: 0;
							bottom: 30px;
							z-index: 5;
						}
					}
				}
			}

			.img-viewer {
				position: relative;
				display: block;
				background: {
					color: $colorBlack;
					repeat: no-repeat;
					position: center;
				}
				height: 100%;
				z-index: 1;
			}

			.video-box {
				position: static;
			}
		}

		.tagged-prod-wrap {
			position: relative;
			background: #fff;
			z-index: 2;
			min-height: calc( 100vh - 54px );

			.tagged-prod-title {
				height: 46px;
				position: -webkit-sticky;
				position: sticky;
				top: 0;
				background: #fff;
				border-top: 1px solid $colorBlack;
				z-index: 2;

				.toggle-title-box {
					height: 100%;

					.toggle-title {
						font-size: 1.6rem;
						@include ellipsis;
						display: block;
					}
				}

				&.sps-blw {
					position: fixed;
					left: 0;
					right: 0;

					& ~ .tagged-prod-list {
						padding-top: 45px;
					}
				}

			}

			.tagged-prod-list {
				.lookbook-contents-wrap {
					.lookbook-contents-item {
						background-color: #fff;

						.lookbook-description {
							padding: 15px 15px 0;
							font-size: $bodyText2;
							line-height: 2.8rem;
							letter-spacing: -0.023rem;
						}

						.prod-grid-list {
							+ .gate-banner-wrap {
								margin-top: 23px;
							}
						}

						.gate-banner-wrap {
							margin-top: 30px;

							.btn-swiper-arrow-wrap {
								display: block;
							}
						}
					}
				}

			}
		}

	}

	.ui-dialog-contents {
		.dialog-contents {
			.cont-padding {
				padding: 0 15px;
			}
		}

		// s: 결제수단 변경
		&.dialog-payment-change {
			.payment-info-wrap {
				padding: 15px 0 0;
				border-top-color: $colorLightestGray;
				@include justify_width;
				justify-items: center;
				align-items: center;

				.payment-type {
					width: 60px;
					padding-top: 0;
				}

				.total-price {
					width: calc(100% - 60px);
					font-size: $bodyText3;
					font-weight: 500;
					text-align: right;
				}
			}
		}

		// e: 결제수단 변경

		// s: 리뷰작성
		&.dialog-review-write {
			.prod-item {
				padding: {
					top: 15px;
					bottom: 15px;
				}
				border-bottom: 1px solid $colorLightestGray;
			}

			.tbl-form {
				.form-box {
					&.horizontal {
						align-items: center;

						.form-title {
							flex: 0 0 75px;
							width: 75px;
						}

						.form-cont {
							flex: 1 1 calc(100% - 75px);
							width: calc(100% - 75px);

							.ui-row-list {
								li ~ li {
									margin-left: 20px;
								}
							}
						}

						~ .horizontal {
							margin-top: 22px;
						}
					}
				}
			}
		}

		// e: 리뷰작성

		// s: 포토리뷰
		&.dialog-photo-review {
			.dialog-contents {
				.photo-review-cnt {
					display: block;
					padding: 19px 15px 0;
					font-size: 1.2rem;
				}

				.photo-review-thumb-wrap {
					padding: 7px 15px 0;

					.swiper-wrapper {
						.swiper-slide {
							.prod-review-img-thumb {
								&:before {
									content: '';
									position: absolute;
									background: #fff;
									top: 0;
									left: 0;
									right: 0;
									bottom: 0;
									z-index: 2;
									opacity: 0.5;
									-webkit-transition: opacity $transDefaultTime $transDefaultEase;
									transition: opacity $transDefaultTime $transDefaultEase;
								}

								&.selected {
									&:before {
										opacity: 0;
									}
								}
							}
						}
					}
				}
			}
		}

		// e: 포토리뷰

		&.dialog-card-benefit, &.dialog-installment, &.dialog-coupon {
			.dialog-contents {
				.cont-padding {
					padding: {
						top: 15px;
						bottom: 15px;
					}

					&.btn-even-wrap {
						padding-top: 0;
					}
				}
			}
		}

		// s: 사이즈 가이드
		&.dialog-size-guide {
			.dialog-title {
				border-bottom-color: $colorBlack;
			}

			.dialog-contents {
				padding-bottom: 15px;

				.tab-wrap {
					.tabs {
						border-top: 0;
					}
				}

				.actual-size-guide-wrap, .common-size-guide-wrap {
					.tbl-col {
						overflow-x: auto;

						table {
							min-width: 100%;
							width: auto;

							thead, tbody {
								tr {
									th, td {
										min-width: 66px;
										white-space: nowrap;
									}
								}
							}
						}
					}
				}
				// s : 실측 사이즈
				.actual-size-guide-wrap {

					.actual-size-img-wrap {
						text-align: center;

						img {
							max-width: 100%;
						}

						~ .sctitle-wrap {
							margin-top: 40px;
						}
					}

					.sctitle-wrap {
						margin-top: 15px;
					}
				}

				// e : 실측 사이즈
				.common-size-guide-wrap {
					.sctitle-wrap {
						margin-top: 15px;

						~ .sctitle-wrap {
							margin-top: 20px;
						}
					}
				}
			}
		}

		// e: 사이즈 가이드

		&.dialog-zip-code, &.dialog-zip-code-search {
			.zip-code-content {
				padding: 15px;

				.no-data {
					&:not(.full), &:not(.has-line) {
						padding: 65px 0;
					}
				}
			}
		}

		&.dialog-zip-code-search {
			.zip-code-content {
				@include justify_width;
				flex-direction: column;
				flex: 1;

				.zipcode-result {
					@include justify_width;
					width: 100%;
					flex: 1;
					align-self: center;

					.address-list {
						.btn-address-select {
							&:after {
								display: none;
							}
						}
					}

					.no-data {
						@include justify_width;
						//flex-direction: column;
						align-items: center;

						.desc {
							flex: 1;
						}
					}
				}
			}
		}

		// s: 태그된 상품
		&.dialog-tagged-prod {
			max-height: calc(100vh - 220px) !important;

			@include device_horizontal {
				max-height: calc(100vh - 45px) !important;
			}

			.dialog-title {
				padding: 0 18px;
				border-bottom: 1px solid $colorLightestGray;
				line-height: 44px;

				.pop-title {
					font-size: $bodyText2;
				}
			}

			.dialog-contents {
				height: calc(100% - 265px);
				max-height: calc(100vh - 265px) !important;

				@include device_horizontal {
					height: calc(100% - 90px);
					max-height: calc(100vh - 90px) !important;
				}
			}
		}

		// e: 태그된 상품


		// s: 임직원전환
		.staff-wrap-layer {
			.cont-padding {
				padding: 30px 15px 70px !important;
			}
			.tit-big {
				margin: 0 0 20px;
				text-align: center;
			}
			.tbl-form {
				.form-box {
					+ .form-box {
						margin-top:-1px;
					}
					:focus {
						position: relative;
						z-index: 10;
					}
					.certification-box {
						margin-top:5px;
					}
				}
				.valid-check.fail {
					.valid-desc {
						margin: 3px 0 10px;
					}
				}
			}
			+ .footer-button {
				padding: 15px;
				position: fixed;
				background: #fff;
				left: 0;
				bottom: 0;
				z-index: 10;
				width: 100%;
				.btn {
					width: 100%;
				}
			}
			.hr {
				margin-top:20px;
			}
		}
		// e: 임직원전환

		// s: 회원가입유도 
		.combine-wrap-layer {
			.cont-padding {
				padding: 0 15px 120px !important;
			}
			.info-desc {
				font-weight: 400;
			}
			.box-gray {
				margin:19px 0 22px;
				padding: 17px 0 16px;
				background:#f2f2f2;
				text-align: center;
				img {
					width:223px;
				}
			}
			.box-line {
				display: block;
				margin:10px 0 0;
				height:40px;
				line-height:40px;
				text-align: center;
				font-size:1.2rem;
				color: #666;
				letter-spacing: -0.03rem;
				border:1px solid #ccc;
				box-sizing: border-box;
			}
			+ .footer-button {
				padding: 15px;
				position: fixed;
				background: #fff;
				left: 0;
				bottom: 0;
				z-index: 10;
				width: 100%;
				.info-desc {
					font-size: 1.2rem;
					text-align: center;
				}
				.btn {
					margin-top:20px;
					width: 100%;
				}
			}
		}
		// e: 회원가입유도

	}

	&.dialog-loading {
		margin: 0;
		background: none;
		border: none;

		.ui-dialog-titlebar {
			display: none;
		}
		.ui-dialog-contents {
			.loading-motion {
				position: relative;
				height: 40px;
				margin: 0 auto;
			}
			.loading-text {
				margin-top: 7px;
				text-align: center;
				text-transform: uppercase;
				font-size: 1.1rem;
			}
		}
	}

	.delivery-info-box {
		.tbl-form, .tbl-wrap {
			margin-top: 20px;
		}

		.tbl-form {
			border: {
				top: 1px solid $colorBlack;
			}
		}
	}
}


/* 세로모드 */
@media all and (orientation: portrait) {
	.fixed-img-wrap .img-viewer {
		background-size: 100% auto;
	}
	.fixed-img-wrap .img-viewer.full {
		background-size: cover;
	}

}

/* 가로모드 */
@media all and (orientation: landscape) {

	.fixed-img-wrap .img-viewer,
	.fixed-img-wrap .img-viewer.full {
		background-size: auto 100%;
	}

}