@import 'scss_header';

.ratio-img-wrap {
	position: relative;
	display: block;
	overflow: hidden;

	.img-viewer {
		position: relative;
		display: block;
		background: {
			color: #ccc;
			repeat: no-repeat;
			position: center;
			size: cover;
		}
		padding-top: 133.3333%;
		z-index: 1;

		.banner-text-wrap {
			position: absolute;
			width: 100%;
			padding: 15px;
			left: 0;
			bottom: 0;

			.banner-text {
				display: block;
				padding: 7px 15px;
				color: #000;
				background: rgba(#fff, .8);
				text-align: center;
				font-size: $bodyText5;
				line-height: 1.6rem;
				word-break: keep-all;
				word-wrap: break-word;

				+ .banner-text {
					margin-top: 5px;
				}
			}
		}
	}
}

.category-header {
	position: relative;
	z-index: 2;

	.js-category-detail-banner {
		border-bottom:1px solid #000;
		.swiper-slide {
			width: calc(100% - 60px);
			position: relative;
			.ratio-img-wrap {
				.img-viewer {
					padding-top: 53.333%;
				}
			}
			& + .swiper-slide:before {
				display: block;
				height:100%;
				position: absolute;
				left:-1px;
				top:0;
				content:"";
				border-left: 1px solid #000;
				z-index:1;
			}
			.ratio-img-wrap {
				border-bottom:0;
			}
		} 
	} 

	.video-box {
		.ratio-img-wrap {
			.img-viewer {
				padding-top: 44.4444%;
			}
		}
	}

	.ratio-img-wrap {
		border-bottom: 1px solid $colorBlack;

		.img-viewer {
			padding-top: 44.4444%;

			&:before, &:after {
				content: '';
				position: absolute;
			}
		}
	}

}

.prod-item {
	vertical-align: top;
	text-align: center;

	.prod-link {
		display: block;
	}

	.ratio-img-wrap {
		display: block;

		.img-viewer {
			display: block;

			.manual-badge-wrap {
				position: absolute;
				width: 133.3333%;
				left: 100%;
				top: 0;
				bottom: 24.76%;
				padding: 5px 0;
				transform: rotate(90deg);
				transform-origin: 0 0;
				@include justify_width(start);
				display: flex;
				flex-wrap: wrap-reverse;
				align-items: flex-end;
				align-content: flex-start;

				.manual-badge {
					display: block;
					/*height: 18px;*/
					margin-bottom: 1px;
					margin-right: 0;
					align-self: flex-start;
					-webkit-align-items: flex-start;
					word-break: break-word;

					+ .manual-badge {
						margin-left: -1px;
					}
				}
			}


		}

		.btn-item-equal {
			position: absolute;
			right: 0;
			bottom: 0;
			z-index: 1;

			&.selected {
				/*right: 0;
				opacity: 1;*/
			}
		}

		&:before, &:after {
			content: '';
			position: absolute;
		}
	}

	.info-wrap {
		position: relative;
		display: block;
		text-align: left;

		.prod-brand-name {
			margin-top: 10px;
			font-size: $bodyText6;
			line-height: 1.4rem;
			font-weight: 700;
			@include ellipsis;
			display: block;
		}

		.prod-name {
			margin-top: 6px;
			font-size: $bodyText6;
			@include multi_ellipsis(1.6rem, 2);
		}

		.prod-price-wrap {
			margin-top: 10px;
			word-break: break-all;
			@include justify_width(start);

			.prod-price-value, .discount-percent {
				display: inline-block;
			}

			.prod-price-value {
				-ms-flex: 0 1 auto;
				flex: 0 1 auto;
				line-height: 1.5rem;
				@include justify_width(start);
				flex-wrap: wrap;
				/*flex-wrap: wrap-reverse;*/

				.prod-price {
					font-size: $bodyText6;
					margin-right: 4px;
					font-weight: 500;
				}

				.prod-price-normal {
					color: $colorLightGray;
					font-size: 0.9rem;
					vertical-align: top;
					text-decoration: line-through;
				}
			}

			.discount-percent {
				-ms-flex: 1 0 40px;
				flex: 1 0 40px;
				color: $pointColor;
				text-align: right;
				font-size: $bodyText5;
				line-height: 1.5rem;
				/*align-self: flex-end;*/

				.percent-value {
					display: inline-block;
					vertical-align: bottom;
				}
			}
		}

		.auto-badge-wrap {
			display: block;
			margin-top: 6px;
		}
	}

	&.col-1 {

	}

	&.col-2 {
		width: 25%;
		color: #fff;
		//font-weight: 700;

		.ratio-img-wrap {
			.img-viewer {
				padding-top: calc((50% - 15px) * 1.33333);
			}
		}
	}

	&:hover, &:focus {
		.btn-item-equal {
			right: 0;
			opacity: 1;
		}
	}

	&.sold-out {
		//pointer-events: none;
		.ratio-img-wrap {
			.img-viewer {
				opacity: .5;
			}
		}

		.info-wrap {
			opacity: .5;

		}
	}

	&.selected {
		.ratio-img-wrap {
			&:before {
				content: '';
				position: absolute;
				display: inline-block;
				width: 100%;
				height: 100%;
				background: rgba($pointColor, .3);
				pointer-events: none;
				left: 0;
				top: 0;
				z-index: 2;
			}
		}
	}

	&.individual {
		position: relative;
		width: 100%;
		padding: 0;
		line-height: 0;
		text-align: left;

		.prod-link {
			position: relative;
			@include justify_width(start, true);
			width: 100%;
			padding: 0;
		}

		.ratio-img-wrap {
			width: 75px;
			height: 100px;

			.ui-chk {
				position: absolute;
				top: -2px;
				left: 0;
				z-index: 2;

				label {
					span {
						@include offscreen;
					}
				}
			}
		}

		.info-wrap {
			width: calc(100% - 75px);
			padding: 0 0 0 15px;

			.prod-brand-name {
				margin-top: 0;
				font-size: $bodyText5;
				@include ellipsis;
				line-height: 1.5rem;
			}

			.prod-name {
				margin-top: 8px;
				font-size: $bodyText4;
			}

			.prod-desc {
				margin-top: 10px;

				.desc-value {
					display: block;
					font-size: $bodyText5;
					color: $colorLightGray;
					line-height: 1.6rem;

					&.option {
						color: $colorDarkGray;
					}

					&.reservation {
						color: $colorBlack;
					}

					+ .desc-value {
						margin-top: 3px;

						&.option {
							margin-top: 9px;
						}

						&.reservation {
							margin-top: 13px;
						}
					}
				}
			}

			.prod-custom-order {
				display: block;
				margin-bottom: 7px;
				font-size: $bodyText5;
				/*@include ellipsis;*/
				@include multi_ellipsis(1.8rem, 2);

				+ .prod-name {
					margin-top: 0;
				}
			}

			.prod-soldout {
				display: block;
				margin-bottom: 4px;
				font-size: $bodyText5;
				line-height: 1.9rem;
			}

			.change-option {
				display: block;
				margin-top: 5px;
				font-size: $bodyText5;
				line-height: 1.8rem;
			}
		}

		.prod-price-wrap {

			.prod-price-value {
				line-height: 1.8rem;

				.prod-price {
					font-size: $bodyText3;
				}

				.prod-price-normal {
					text-decoration: initial;
				}
			}

			.discount-percent {
				-ms-flex: 0 1 auto;
				flex: 0 1 auto;
				text-align: left;
				font-size: 1.4rem;
				font-weight: 500;
				line-height: 1.8rem;

				+ .prod-price-value {
					margin-left: 8px;
				}
			}
		}

		.prod-fix-btn-wrap {
			position: absolute;
			right: -10px;
			bottom: -6px;

			.btn-link {
				padding: 10px;
			}
		}

		.btn-even-wrap {
			margin: 13px 0 0 90px;
		}

		.prod-action-status {
			@include justify_width(start);
			flex-wrap: wrap;
			margin: 10px 0 0 90px;

			.action-type {
				padding-right: 10px;
				display: block;
				font-size: $bodyText4;
				line-height: 1.9rem;
			}

			.action-detail {
				flex: 1;
				margin-top: 1px;
				text-align: right;

				li {

					.claim-desc {
						font-size: $bodyText6;
						word-break: keep-all;
						word-wrap: break-word;
						line-height: 1.7rem;
					}

					.detail-desc {
						@include justify_width(end);
						align-items: center;
						line-height: 1.7rem;

						.detail-text {
							display: inline-block;
							font-size: $bodyText6;
							vertical-align: top;

							&.point {
								margin-right: 8px;
								color: #f00;
							}
						}

						.btn-link {
							margin: {
								left: 4px;
								bottom: 0;
							}
							vertical-align: top;
						}
					}

					.btn-link {
						margin-bottom: 6px;
					}

					~ li {
						margin-top: 2px;
					}
				}

			}

			.action-btn-box {
				flex: 0 1 100%;
				margin-top: 4px;
				text-align: right;
			}
		}

		.prod-claim-wrap {
			margin-top: 15px;

			.ip-byte-check {
				height: 100px;
			}

			.change-option {
				display: block;
				padding: 5px 0 10px;
				font-size: $bodyText5;
				line-height: 1.8rem;
			}

			&.is-hide {
				display: none;
			}
		}

	}
}

.prod-grid-list {
	overflow: hidden;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: flex-start;
	-webkit-justify-content: flex-start;
	flex-wrap: wrap;
	padding: 8px 8px 0;

	.prod-item {
		width: 50%;
		padding: 7px;

		&:nth-child(2) ~ .prod-item {
			margin-top: 16px;
		}
	}


	.no-data {
		padding: 72px 0 50px;
	}

	&.col-1 {
		.prod-item {
			width: 100%;

			~ .prod-item {
				margin-top: 16px;
			}
		}
	}

}

/* s: 상품 상세 */
.prod-detail-wrap {
	word-break: keep-all;
	word-wrap: break-word;

	.prod-detail-content {
		.prod-brand-info {
			padding: 8px 15px;
			border-bottom: 1px solid $colorLightestGray;
		}

		.prod-detail-info {
			padding: 20px 15px 0;

			.prod-default-info {
				.prod-name {
					font-size: 1.5rem;
					line-height: 2.4rem;
				}

				.prod-price-info {
					margin-top: 11px;

					.prod-price-inner {
						@include justify_width(center, true);
						justify-content: center;
						align-items: center;

						.discount-percent {
							display: inline-block;
							margin-right: 6px;
							font-size: 2rem;
							font-weight: 500;
							line-height: 2.5rem;
							color: #f00;
							@include mob_small {
								font-size: 1.7rem;
							}
						}

						.price-value {
							display: inline-block;
							margin-right: 6px;
							font-size: 2rem;
							font-weight: 500;
							line-height: 2.5rem;
							@include mob_small {
								font-size: 1.7rem;
							}
						}

						.price-value-normal {
							padding-top: 2px;
							display: inline-block;
							font-size: $bodyText5;
							font-weight: 300;
							color: $colorLightGray;
							text-decoration: line-through;
							@include mob_small {
								font-size: 1rem;
							}
						}
					}

					.btn-coupon-download {
						margin: {
							/*top: -1px;*/
							left: 20px;
						}

						@media screen and (max-width: 360px) {
							margin-left: 10px;
							padding: 0 8px;
						}
					}
				}

				.additional-benefits {
					@include justify_width;
					margin-top: 20px;
					padding: 20px 0;
					border: {
						top: 1px solid $colorLightestGray;
						bottom: 1px solid $colorLightestGray;
					}

					dt {
						font-size: $bodyText6;
						line-height: 2rem;
					}

					dd {
						flex: 1;
						padding-left: 17px;
						font-size: $bodyText6;
						line-height: 2rem;
						letter-spacing: normal;
						text-align: left;
						color: #333;

						.has-btn-card {
							@include justify_width;

							.info {
								flex: 1 0 calc(100% - 74px);
							}

							.btn-link {
								align-self: center;
							}
						}
					}
				}
			}

			.prod-detail-fold-wrap {
				border: 1px solid $colorLightenGray;
				border-bottom: 0;

				.fold-box-list {
					.fold-box {
						.fold-box-header {
							.toggle-title-box {
								border-bottom-color: $colorLightenGray;
								.ico-toggle {
									margin-left: 8px;
								}
							}
						}
						&.expanded {
							.fold-box-contents {
								border-bottom-color: $colorLightenGray;
								white-space: pre-line; 
							}
						}
					}
					&.fold-info {
						.fold-box {
							&.expanded {
								.fold-box-contents {
									padding-top: 0;
									font-size: 11px;
									line-height: 1.9;
									color: #333;
								}
							}
						}
					}
				}
			}

			.count-down-wrap {
				margin-top: 30px;
			}

			.prod-detail-fold-wrap {
				margin-top: 30px;
			}

			.profile-flex-wrap {
				margin-top: 30px;
				&.txt-wrap {
					padding: 0 15px;
					margin-top: 15px;
					height: 165px;
					background: no-repeat 50%;
					background-size: cover;
					position: relative;
					border: 1px solid #ccc;
					&:before {
						display: block;
						width: 100%;
						height: 100%;
						position: absolute;
						left: 0;
						top: 0;
						z-index: 0;
						background-color: #000;
						opacity: 0.5;
						content: "";
					}
					a {
						display: block;
						position: relative;
						z-index: 1;
					}
					.fullname {
						font-size: 1.6rem;
						color: #fff;
					}
					.txt-box {
						margin-top: 35px;
						font-size: 1.3rem;
						color: #fff;
						.txt {
							line-height:1.7;
							overflow: hidden;
							text-overflow: ellipsis;
							display: -webkit-box;
							-webkit-line-clamp: 3; 
							-webkit-box-orient: vertical;
						}
					}
				}
			}

			.evt-banner {
				margin-top: 15px;
				min-height:90px;
				border: 1px solid #ccc;
				background-size: cover;
				a {
					display: block;
					padding: 19px;
				}
				.tit {
					display: block;
					font-size: 1.6rem;
					line-height: 2.2rem;
					font-weight: 500;
				}
				.tit-sm {
					display: block;
					margin-top:10px;
					font-size: 1.1rem;
				}
			}
		}

		.prod-detail-prod-wrap {
			margin-top: 10px;
		}

		.prod-detail-tab-wrap {
			padding-top: 50px;

			.tab-wrap {
				position: relative;
				padding-top: 40px;

				.tabs {
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					background: #fff;
					border: {
						top: 1px solid $colorBlack;
						bottom: 1px solid $colorBlack;
					}
					.tab-item {
						border-left-color: black;
						&.is-active {
							background-color: #000;
							.tab-link {
								color: #fff;
							}
						}						
						.tab-link {
							font-size: 1.5rem;
							font-weight: 300 !important;
							color:#000;
						}
					}

					&.sps-blw {
						z-index: 2;
						position: fixed;
						top: calc(#{$headerHeightDefault} - 1px);
						&+ .tab-content {
							margin-top: -1px;
						}
					}
				}

				.tab-content {

					// s : 디테일 탭 
					&#tabContentDetail {
						position: relative;
						max-height: 1000px;
						overflow: hidden;
						&.on {
							max-height: none;
							.btn-detail {
								span {
									&:after {
										content:" 접기"
									}
								}
							}
						}
						.btn-detail {
							display: block;
							height: 40px;
							line-height: 40px;
							font-size: 1.1rem;
							text-align: center;
							position: absolute;
							left: 15px;
							right: 15px;
							bottom: 15px;
							span {
								position: relative;
								z-index: 1;
								&:after {
									content:" 더 보기"
								}
							}
							&:before {
								display: block;
								content:"";
								width: 100%;
								height: 100%;
								position: absolute;
								left: 0;
								top: 0;
								z-index: 0;
								background: #fff;
								opacity: 0.9;
								box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
							}
						}
						.prod-detail-box {
							.prod-detail-wrap-inner {
								padding: 20px 15px;
								.prod-detail-title {
									font-size: 1.5rem;
									line-height: 1.5;
									letter-spacing: -0.38px;
									font-weight: 500;
								}
				
								.editor-wrap {
									margin-top:-15px;
									font-size: 1.3rem;
									white-space: pre-line;
									line-height: 1.7;
									letter-spacing: -0.33px;
								}
								&+ .prod-detail-wrap-inner {
									border-top: 1px solid #f2f2f2
								}
							}
						}
					}
					// e : 디테일 탭

					// s : 리뷰 & QNA
					&.write-cont-tab {
						padding-top: 12px;
						background: #f2f2f2;
						position: relative;
						text-transform: uppercase;
						.btn-more {
							text-transform: uppercase;
							margin-bottom: 50px;
						}
						.no-data {
							margin: 0;
							padding: 15px 0 55px;
							border-bottom: 0;
							.desc {
								text-align: left;
							}
						}
					}
					// e : 리뷰 & QNA

					.editor-wrap {
						font-size: 1.3rem;
						line-height: normal;
						font-family: "GT America","Apple SD Gothic Neo","Noto Sans KR","Noto Sans TC","Noto Sans JP",sans-serif !important;
						white-space: normal;
						word-break: break-word;

						p:not(.star-text) {
							margin: 15px;
							font-size: 1.3rem;
							line-height: normal;
							font-family: "GT America","Apple SD Gothic Neo","Noto Sans KR","Noto Sans TC","Noto Sans JP",sans-serif !important;
							white-space: normal;
							word-break: break-word;
						}

						[align] {
							margin: 0 15px;
						}
					}

					.stitle {
						padding-top: 15px;
						margin-bottom: 10px;
						text-transform: uppercase;
						font-size: 1.5rem;
						line-height: 1.9rem;
						font-weight: 400;
					}

					.stitle-wrap {
						padding-top: 15px;
						margin-bottom: 10px;

						.stitle {
							padding-top: 0;
							margin-bottom: 0;
						}
					}

					.no-data {
						border-bottom: solid 1px $colorLightestGray;
						margin-bottom: 20px;
					}

					.prod-review-info-box {
						background: #fff;
						border: solid 1px $colorLightenGray;

						.fold-box.expanded {
							.fold-box-contents {
								.prod-review-info {
									.review-info-flex-box {
										~ .review-info-flex-box {
											@include justify_width(center);
										}
									}

									~ .prod-review-info {
										margin-top: 16px;
									}
								}

							}
						}

						.toggle-title-box {
							border-bottom-color: $colorLightenGray;

							.toggle-title {
								font-size: 1.3rem;
								line-height: 1.9rem;
							}

							.toggle-sub-title {
								font-size: 1.1rem;
								line-height: 1.7rem;
								color: $colorLightGray;
							}

							.right-box {
								margin-bottom: 1px;
								@include justify_width(start, true);
							}

							.rating-container { 
								font-size: 0;
								height: 12px;
								align-self: center; 
								.rating-stars {
									.filled-stars {
										min-width: 0 !important;
									}
								}
							}

							.ico-toggle {
								margin-left: 10px;
							}
						}

						.fold-box-contents {
							padding: 15px;

							.prod-review-info {
								@include justify_width;

								.review-info-title {
									width: 60px;
									font-size: 1.2rem; 
									line-height: 1.8rem;
								}

								.review-info-content {
									flex: 1 0 auto;
								}

								.review-info-flex-box {
									@include justify_width(center);

									align-items: center;

									.review-info-sub-title {
										flex: 0 0 44px;
										font-size: 1.1rem;
										line-height: 1.7rem;
										color: $colorDarkGray;
									}

									.review-info-bar-box {
										flex: 1;
										height: 2px;
										background: #ccc;

										.review-info-bar {
											height: 100%;
											background: #000;
										}
									}

									.review-info-value {
										flex: 0 0 36px;
										font-size: 1.1rem;
										line-height: 1.4rem;
										color: $colorDarkGray;
										text-align: right;
									}

									~ .review-info-flex-box {
										display: none;
										margin-top: 4px;
									}
								}

								~ .prod-review-info {
									margin-top: 5px;
								}
							}
						}
					}

					// s:포토리뷰 썸네일 리스트
					.photo-review-thumb-wrap {
						margin: 15px -15px 0;
						padding: 0 15px;
					}

					// e:포토리뷰 썸네일 리스트
					// s: 리뷰
					.review-detail-wrap {
						margin: 15px -15px 0;
						padding: 0 15px;
						background: #fff;

						.list-sort-wrap {
							margin: 0 -15px;
							padding: 10px 15px;
						}

						.review-fold-wrap {
							.fold-box {
								border-bottom: solid 1px $colorLightestGray;
								padding: 15px 0;

								.fold-box-header-title {
									&.has-review-thumb {
										.review-content-wrap {
											.review-info-wrap {
												max-width: calc(100% - 95px);
												@include mob_small {
													max-width: calc(100% - 90px);
												}
											}

											.prod-review-img-thumb {
												width: 80px;
												flex: 0 1 80px;
												margin-left: 15px;

												.review-img-thumb {
													vertical-align: top;
												}

												@include mob_small {
													width: 75px;
													flex: 0 1 75px;
													margin-left: 10px;
												}
											}
										}
									}
								}

								.review-content-wrap {
									flex-wrap: nowrap;

									.rating-list {
										li {
											.type {
												font-size: $bodyText6;
												margin-right: 4px;
											}

											.val {
												font-size: $bodyText6;
											}

											.rating-container {
												margin-top: 1px;
											}

											~ li {
												margin-left: 10px;
											}
										}
									}

									.review-desc {
										line-height: 2rem;
										max-height: 20px;
									}

									.profile-name {

										.profile-detail {
											@include justify_width(start, true);
											position: relative;
											overflow: hidden;
										}
									}
								}

								&.expanded {
									.fold-box-header-title {
										&.has-review-thumb {
											.review-content-wrap {
												.review-info-wrap {
													word-break: keep-all;
													word-wrap: break-word;
													max-width: 100%;
												}
											}
										}
									}

									.review-content-wrap {
										flex-direction: column;

										.review-info-wrap {
											.review-desc {
												@include clear_multi_ellipsis;

												white-space: pre-line;

												p {
													display: block;
												}

												.review-desc-img {
													display: block;
													margin-top: 10px;
												}
											}
										}

										.prod-review-img-thumb {
											display: none;
										}
									}
								}
							}
						}
					}

					// e: 리뷰
					// s: Q&A
					.qna-detail-wrap {
						margin:0 -15px;
						padding: 0 15px;
						background: #fff;

						.qna-fold-wrap {
							.fold-box-list {
								.fold-box {

								}
							}
						}
					}

					// e: Q&A
				}

				&.sps-blw {
					.tabs.sps-blw {
						position: absolute;
						top: initial;
						bottom: 0;
					}
				}
			}
		}

		.prod-info-wrap.cont-padding {
			padding: {
				top: 15px;
				bottom: 15px;
			}
			border-top: 1px solid $colorBlack;

			.fold-box-list {
				border: solid 1px $colorLightenGray;
				border-bottom: 0;
				.fold-box-header {
					&.header-only {
						.ico-toggle {
							display: none;
						}
					}
				}
				.toggle-title-box {
					border-bottom-color: $colorLightenGray;
					
					.ico-toggle {
						margin-left: 8px;
					}
				}
				.fold-box-contents {
					border-bottom-color: $colorLightenGray;
					&.prod-info-type {
						padding: 0;
					}

					.text-list {
						margin-top: 0;

						li {
							line-height: 2rem;

							~ li {
								margin-top: 0;
							}
						}
					}

					.tbl-size-info {
						margin: -1px;
						overflow-x: auto;
						table {
							min-width: 100%;
							width: auto;
							th, td {
								padding: 10px 15px;
								font-size: 1.1rem;
								min-width: 66px;
								white-space: nowrap;
								background: #fff;
								text-align: center;
							}
							th:first-child,
							td:first-child {
								padding: 10px 0;
								width: 80px;
								position:sticky;
								left: 0;
								&:before {
									display: block;
									position: absolute;
									width: 100%;
									height: 100%;
									left: 1px;
									top: 0;
									border-right: 1px solid #ccc;
									content: "";
								}
							}
							thead {
								th:first-child {
									&:after {
										display: block;
										position: absolute;
										width: 162px;
										height: 100%;
										left: -83px;
										top: 0;
										border-top: 1px solid #ccc;
										content: "";
										transform: rotate(27deg);
									}
								}
							}
						}
					}
				}
			}

			.prod-delivery-title {
				font-size: $bodyText5;
				font-weight: 500;
				color: #333;

				~ .prod-delivery-title {
					margin-top: 30px;
				}

				~ .editor-wrap {
					font-size: 1.1rem;
					line-height: 1.8;
					white-space: pre-line;
					color: #333;
				}
			}

			.kc-mark-box {
				img {
					width: 28px;

					+ p {
						margin-top: 6px;
					}
				}
			}

			.label-box {
				position: relative;
				padding-top: 100%;
				margin: 0 auto;
				background: $colorLightestGray;

				img {
					position: absolute;
					top: 0;
					left: 50%;
					height: 100%;
					transform: translateX(-50%);
				}

				// 가로 화면
				@include device_horizontal {
					width: 174px;
					padding-top: 174px;
				}

				// 디자인 사이즈 보다 클 경우
				@include mob_middle {
					width: 174px;
					padding-top: 174px;
				}
			}

		}

		.prod-relation-prod-wrap {
			border-top: 1px solid $colorBlack;
		}

		.gate-banner-wrap {
			margin: 35px 0 -31px;
		}
	}

}

.label-box {
	display: block;
	width: 100%;
	padding: 15px 0;

	img {
		display: block;
		max-width: 100%;
		margin: 0 auto;
	}
}

/* e: 상품 상세 */

.prod-relation-prod-wrap {
	.stitle {
		padding-top: 0;
	}

	.item-box {
		padding: 20px 0 34px;

		.prd-list-swiper-wrap {
			margin: {
				left: -45px;
				right: -15px;
			}
		}

		&:last-child {
			padding-bottom: 4px;
		}

		~ .item-box {
			border-top: solid 1px $colorLightenGray;
		}
	}
}

.sticky-popup {
	display: none;
	z-index: 1005;
	position: fixed;
	left: 0;
	right: 0;
	top: $headerHeightDefault;

	.sticky-text {
		padding: 7px 5px 8px;
		font-size: 1rem;
		line-height: 1.5rem;
		text-align: center;
		color: #fff;
		background: rgba(0, 0, 0, 0.8);

		&.gray-type {
			color: $colorBlack;
			background: rgba(204, 204, 204, 0.8);
		}

		opacity: 0;
	}

	&.is-active {
		display: block;

		.sticky-text {
			opacity: 1;
		}
	}

	&.is-moction {
		display: block;

		.sticky-text {
			animation: stickyAnimation 1.5s forwards;
			-webkit-animation: stickyAnimation 1.5s forwards;
		}
	}
}

@keyframes stickyAnimation {
	0% {
		opacity: 0;
	}

	25% {
		opacity: 1;
	}

	65% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}