@import 'scss_header';

.tbl-wrap {

	.tbl-header {
		@include justify_width;

		.tbl-header-title {
			font-size: $bodyText3;
			font-weight: 500;
		}

		.tbl-header-unit {
			color: #999;
			font-size: $bodyText5;
		}

		+ table {
			margin-top: 16px;
		}
	}

	.tbl-footer {
		@include justify_width;
		margin-top: 20px;

		.btn-wrap {
			margin-top: 0;
			text-align: left;

			[class*=btn-] {
				+ [class*=btn-] {
					margin-left: -1px;
				}
			}
		}
	}

	// s : 상품 정보 테이블
	&.tbl-prod-info {
		table {
			tbody {
				tr {
					th, td {
						padding: 12px 15px 11px;
						border: {
							bottom: 1px solid $colorLightestGray;
						}
						text-align: left;
						font-size: $bodyText6;
						line-height: 1.7rem;
					}

					th {
						border: {
							right: 1px solid $colorLightestGray;
						}
					}

					td {

					}

					&:last-child {
						th, td {
							border-bottom: none;
						}
					}
				}
			}
		}
	}

	// e : 상품 정보 테이블

	// s : 사이즈 가이드 테이블
	&.tbl-size-info {
		table {
			thead, tbody {
				tr {
					th, td {
						height: 41px;
						padding: 11px 19px;
						border: 1px solid $colorLightenGray;
						font-size: $bodyText5;
						line-height: 1.5;
					}
				}
			}

			tbody {
				tr {
					th, td {
						color: $colorDarkGray;
						text-align: center;
					}
				}
			}
		}
	}

	// s : 사이즈 가이드 테이블

	+ .tbl-wrap {
		border-top: none;
	}
}

// s : tbl-form
.wrap {
	.contents-wrap {
		.tbl-form {
			&:first-child {
				margin-top: 15px;
			}
		}
	}
}

.tbl-form {
	.form-box {
		.form-title {
			margin-bottom: 6px;
			font-size: $bodyText5;
			line-height: 2rem;
			color: $colorBlack;

			&:not(.prod-info) {
				@include justify_width(start);
				align-items: center;
			}

			&.prod-info {
				p {
					@include ellipsis;
				}
			}

			.required {
				&:after {
					content: '';
					display: inline-block;
					width: 3px;
					height: 3px;
					margin: 4px 0 0 2px;
					background-color: #f00;
					border-radius: 100%;
					vertical-align: top;
				}
			}

			.right-box {
				margin-left: auto;
			}
		}

		.form-cont {
			font-size: $bodyText5;
			line-height: 2rem;
			word-break: keep-all;
			word-wrap: break-word;
			color: $colorBlack;

			.ui-sel {
				font-size: 1.2rem;

				+ .coupon-price {
					color: $colorDarkGray;
					margin: 10px 0 15px;
					text-align: right;
					font-size: $bodyText3;
					line-height: 2.4rem;

					.unit {
						font-size: $bodyText4;
					}
				}
			}

			.valid-check {
				+ .btn-even-wrap {
					margin-top: 15px;
				}
			}

			.btn-even-wrap {
				margin-top: 0;
			}

			.col-input {
				+ .ui-chk {
					margin-top: 10px;
				}
			}

			.ip-text-wrap {
				position: relative;
				border: 1px solid $colorLightenGray;
				background-color: #fff;
				padding-right: 45px;

				.ui-input {
					width: 100%;
					height: 38px;
					border: none;
				}

				.unit {
					position: absolute;
					right: 9px;
					top: 0;
					font-size: $bodyText6;
					color: $colorBlack;
					line-height: 3.8rem;
				}

				&.disabled {
					background-color: #f8f8f8;
					border-color: #ddd;
				}
			}

			.point-input {
				@include justify_width;
				align-items: center;

				.use-point-box {
					flex: 1;
					@include justify_width;

					.ui-input {
						flex: 1;
					}

					.unit {
						display: inline-block;
						margin-left: 7px;
						font-size: $bodyText4;
						color: $colorBlack;
						vertical-align: top;
						line-height: 40px;
					}
				}

				.ui-chk {
					margin-left: 10px;
				}
			}

			.point-available {
				display: block;
				margin-top: 10px;
				font-size: 1rem;
				color: $colorBlack;
				line-height: 1;
			}

			.certification-box {

				.btn-flex-wrap {
					.btn {
						padding: {
							left: 14px;
							right: 14px;
						}
						min-width: 94px;
						width: 94px;
					}
				}

				&.point {
					display: none;
					margin-top: 10px;
					background-color: $colorLightestGray;
					padding: 13px 15px 15px;

					.col-title {
						display: block;
						margin-bottom: 10px;
						font-size: $bodyText5;
						color: $colorBlack;
						line-height: 1.8rem;
					}

					&.is-active {
						display: block;
					}
				}
			}

			.zip-code-wrap {
				display: none;
				height: 330px;
				margin-top: 5px;

				.zip-code-content {
					height: calc(100% - 35px);
					padding-top: 6px;
				}
			}

			.setting-chk-box {
				margin-top: 15px;
				padding-top: 11px;
				border-top: 1px solid $colorLightestGray;
			}

			.prod-item {
				&.individual {
					padding: 15px 0;
					border-bottom: 1px solid $colorLightestGray;

					.prod-fix-btn-wrap {
						right: auto;
						left: 90px;
						bottom: 0;
					}
				}
			}

			.sns-box {
				@include justify_width;

				.sns-title {
					flex: 0 1 100px;
					display: block;
					font-size: $bodyText6;
					line-height: 1.7rem;
					color: $colorDarkGray;
				}

				.valid-check {
					flex: 1;
				}

				.sns-url {
					display: block;
					margin-bottom: 5px;
					font-size: $bodyText6;
					line-height: 1.7rem;
					color: $colorDarkGray;
				}

				+ .sns-box {
					margin-top: 5px;
				}

				+ .desc-gray {
					margin-left: 100px;
				}
			}

			.profile-edit-wrap {
				@include justify_width(start);

				.my-profile-name {
					width: 56px;
					height: 56px;
				}

				.profile-setting-box {
					margin: -2px 0 0 15px;

					.setting-desc {
						font-weight: 300;
						font-size: $bodyText6;
						line-height: 1.7rem;
					}

					.img-setting {
						margin-top: 6px;
						font-size: 0;

						.file-attach-box {
							display: inline-block;
							margin-left: 5px;
							vertical-align: top;
							line-height: 1;

							input[type=file] {
								position: absolute;
								width: 1px;
								height: 1px;
								padding: 0;
								margin: -1px;
								overflow: hidden;
								clip: rect(0, 0, 0, 0);
								border: 0;
								font-size: $bodyText6;
							}
						}

						.btn-sm {
							min-width: 70px;
						}
					}
				}
			}

			.equal-list {
				li {
					line-height: 1.8rem;
				}
			}

			.desc-gray {
				margin-top: 10px;
			}

			.ui-row-list {
				font-size: 0;

				li {
					display: inline-block;
					vertical-align: top;

					~ li {
						margin-left: 29px;
					}
				}

				$columnCnt: 7; // 컬럼 갯수(2~7)
				@for $i from 1 through $columnCnt {
					&.col-#{$i} {
						li {
							~ li {
								margin-left: 0;
							}
						}
					}
				}

				&.full {
					flex-direction: column;

					li {
						-ms-flex: 0 1 100%;
						flex: 0 1 100%;
						width: 100%;

						~ li {
							margin: {
								left: 0;
								top: 6px;
							}

							.ui-rdo {
								~ .ui-sel {
									margin: {
										top: 6px;
										bottom: 2px;
									}
								}
							}
						}
					}
				}
			}

			.register-address-info {
				position: relative;
				padding: 14px;
				border: 1px solid $colorLightenGray;

				.address-nickname {
					display: block;
					font-size: $bodyText5;
					line-height: 1.8rem;
					padding-right: 65px;
				}

				.btn-link {
					position: absolute;
					top: 12px;
					right: 9px;
					padding: 5px;
				}

				.info-list {
					margin-top: 10px;

					li {
						font-size: 0;
						line-height: 1;

						.name, .phone {
							display: inline-block;
							font-size: $bodyText5;
							line-height: 1.8rem;
							vertical-align: top;
						}

						.phone {
							&:before {
								content: '/';
								display: inline-block;
								margin: 0 3px;
								vertical-align: top;
							}
						}

						.zip-code, .address-detail {
							display: inline-block;
							font-size: $bodyText6;
							vertical-align: top;
						}

						.zip-code {
							width: 40px;
							line-height: 1.4rem;
						}

						.address-detail {
							width: calc(100% - 50px);
							margin-left: 10px;
							font-weight: 300;
							line-height: 1.8rem;
						}

						.cnt {
							display: block;
							font-size: $bodyText5;
							line-height: 1.8rem;
						}

						~ li {
							margin-top: 10px;
						}
					}
				}
			}

			.text-list {
				+ .btn-even-wrap {
					margin-top: 15px;
				}
			}

			.delivery-pay-num {
				display: inline-block;
				margin-right: 20px;
			}
		}

		&.secession-guide {
			padding: 24px 0 20px;
			margin: 21px 0 40px;
			border-top: 1px solid #f2f2f2;
			border-bottom: 1px solid $colorLightenGray;

			.text-list-title {
				margin-top: 0;
			}
		}

		+ .form-box {
			.form-title {
				margin-top: 10px;
			}

			&.horizontal {
				margin-top: 20px;

				.form-title {
					margin-top: 0;
				}
			}
		}

		&.horizontal {
			@include justify_width(start);
			align-items: flex-start;
			flex-wrap: wrap;
			margin-top: 5px;

			.form-title {
				flex: 0 0 100px;
				width: 100px;
				margin: {
					bottom: 0;
					top: 0;
				}
				padding-right: 10px;
			}

			.form-cont {
				flex: 1 1 calc(100% - 100px);
				width: calc(100% - 100px);
				margin-top: 0;

				~ .desc-gray {
					&.star-text {
						margin-top: 10px;
						margin-bottom: -4px;
					}
				}

			}

			~ .horizontal {
				margin-top: 8px;
			}

			+ .form-box {
				&:not(.horizontal) {
					.form-title {
						margin-top: 18px;
					}
				}
			}

			&.pw-info {
				margin-top: 10px;
				align-items: center;
			}

			&.sns-login-setting {
				margin-top: 20px;
				padding-bottom: 15px !important;
				align-items: center;

				.btn-sns-share {
					&.is-active {
						border-color: $colorBlack;
					}
				}
			}
		}

		&.form-box-border, &.attach-file, &.coupon-box {
			border-bottom: 1px solid $colorLightestGray;
		}

		&.attach-file {
			padding-bottom: 15px;

			.form-cont {
				margin-top: -4px;
				font-size: 0;

				.file-wrap {
					margin-top: 10px;
				}
			}

			&.horizontal {
				padding-bottom: 0;
				border-bottom: none;

				.form-cont {
					margin-top: -10px;
				}
			}
		}

		&.coupon-box {
			+ .coupon-box {
				margin-top: 15px;
			}
		}

		&:last-of-type {
			&.horizontal {
				padding-bottom: 19px;
				border-bottom: 1px solid $colorLightestGray;

				+ .text-list-title {
					margin-top: 24px;
				}
			}

			&:not(.horizontal) {
				+ .text-list-title {
					margin-top: 19px;
				}
			}


		}

		&.has-tline {
			margin-top: 20px;
			padding-top: 5px;
			border-top: solid 1px $colorLightestGray;
		}
	}

	> .desc-gray {
		&:first-child {
			margin-bottom: 14px;
			line-height: 1.64;
			letter-spacing: 0;
		}
	}
}

// e : tbl-form

// s : tbl-prod
.tbl-prod {
	border-top: 1px solid $colorBlack;

	.prod-header {
		@include justify_width;
		align-items: center;
		height: 40px;
		border-bottom: 1px solid $colorLightestGray;
		font-size: $bodyText5;

		span {
			display: block;
		}

		.delivery-type {
			color: $colorGray;
		}

		.delivery-price {
			color: $colorBlack;
		}

		.brand-name {
			text-transform: uppercase;
			color: $colorBlack;
			@include ellipsis;
			display: block;
		}

		~ .prod-header {
			border-top: 1px solid $colorBlack;
		}
	}

	.prod-item {
		&.individual {
			padding: 15px 0;
			border-bottom: 1px solid $colorLightestGray;
		}
	}

	.brand-total-price {
		padding: 20px 0;
		text-align: center;

		.value {
			display: inline-block;
			margin-left: 4px;
			font-size: $bodyText3;
			color: $colorDarkGray;
			vertical-align: top;
		}

		.unit {
			display: inline-block;
			font-size: $bodyText4;
			color: $colorDarkGray;
			vertical-align: top;
		}

		.price-item {
			display: inline-block;
			vertical-align: top;
			line-height: 2rem;

			.type {
				display: inline-block;
				font-size: $bodyText4;
				color: $colorLightGray;
				letter-spacing: -0.065rem;
				vertical-align: top;
			}

			+ .price-item {
				&:before {
					content: '+';
					display: inline-block;
					margin: 0 4px;
					font-size: $bodyText4;
					color: $colorDarkGray;
					vertical-align: top;
				}
			}
		}

		.price-total {
			display: block;
			margin-top: 5px;
			line-height: 2rem;

			.value {
				font-weight: 500;
			}

			.btn-alert {
				margin: 1px 0 0 2px;
			}

			&:before {
				content: '=';
				display: inline-block;
				font-size: $bodyText4;
				color: $colorDarkGray;
				vertical-align: top;
			}
		}

		&:last-of-type {
			border-bottom: 1px solid $colorBlack;
		}
	}

	.desc-gray {
		margin-top: 10px;

		&:last-child {
			position: relative;

			&:after {
				content: '';
				position: absolute;
				top: -11px;
				left: 0;
				right: 0;
				display: block;
				border-bottom: 1px solid $colorBlack;
			}
		}
	}

	+ .order-price-wrap {
		margin-top: 30px;
	}

}

// e : tbl-prod

// s: 기본 - col
.tbl-col {
	table {
		border-top: 1px solid $colorBlack;

		th, td {
			border-bottom: 1px solid $colorLightestGray;
			font-family: $fontMix;
		}

		thead {
			th {
				padding: 14px 10px 13px;
				background: #fff;
				font-size: $bodyText5;
				color: $colorBlack;
				font-weight: 500;
				text-align: center;
			}
		}

		tbody {
			th, td {
				padding: 11px 10px 10px;
				font-size: $bodyText5;
				text-align: center;
				line-height: 1.8rem;
				font-weight: 300;
				color: $colorDarkGray;
				word-break: keep-all;
				word-wrap: break-word;

				&.text-right {
					text-align: right;
				}

				&.text-left {
					text-align: left;
				}

				&.input {
					padding: 20px 20px;
				}

				.accumulate-list {
					li {
						@include justify_width;

						~ li {
							margin-top: 2px;
						}
					}
				}

				+ td, + th {
					border-left: 1px solid $colorLightestGray;
				}
			}
		}
	}

	.desc-black {
		margin-top: 10px;
	}

	&.no-line {
		table {
			border-bottom: 1px solid $colorLightestGray;

			th, td {
				padding: {
					left: 0;
					right: 10px;
				}
				text-align: left;
				border-bottom: 0;
				border-left: 0;
				vertical-align: top;
			}

			thead {
				th {
					padding-bottom: 5px;
					line-height: 1.8rem;
				}
			}

			tbody {
				tr {
					th {
						color: #000;
					}

					td, th {
						padding: {
							top: 5px;
							bottom: 5px;
						}

						&:last-child {
							padding-right: 0;
						}
					}

					&:last-child {
						th, td {
							padding-bottom: 20px;
						}
					}
				}
			}
		}

		.text-list {
			margin-top: 15px;
		}
	}
}

// e: 기본 - col




