// 폰트
$fontMix: 'GT America', 'Apple SD Gothic Neo', 'Noto Sans KR', 'Noto Sans TC', 'Noto Sans JP', sans-serif;
$fontEng: $fontMix; //'GT America', 'Apple SD Gothic Neo', sans-serif;
$fontKor: $fontMix; //'Apple SD Gothic Neo', 'Noto Sans KR', 'Noto Sans TC', 'Noto Sans JP', sans-serif;

// 폰트 사이즈 관련
$defaultFontSize: 10px;

$headerTitle1: 10.6rem;
$headerTitle2: 9rem;

$bodyTitle1: 3.4rem;
$bodyTitle2: 3.0rem;
$bodyTitle3: 2.4rem;
$bodyTitle4: 2rem;

$bodyText1: 2rem;
$bodyText2: 1.5rem;
$bodyText3: 1.4rem;
$bodyText4: 1.3rem;
$bodyText5: 1.2rem;
$bodyText6: 1.1rem;

// 헤더 사이즈 관련
$headerHeightDefault: 45px;
$headerHeightDetail: 56px;
$specialBannerHeight: 39px;
$dockerHeightDefault: 60px;
$headerHeightMain: 81px;


// 헤더 사이즈 관련

// 모바일 small 사이즈
$mobSmallWidth: 320px;
$mobMiddleWidth: 720px;

// 컬러
$colorBlack: #000;
$colorDarkGray: #333;
$colorGray: #666;
$colorLightGray: #999;
$colorLightenGray: #ccc;
$colorLightestGray: #f2f2f2;
$pointColor: #f00;


// 트렌지션
$transDefaultTime: .23s;
$transDefaultEase: ease-out;
$transEaseBounceBack: cubic-bezier(.17, .67, .29, 1.32);

// jqueryUI Dialog
$modalPopupSizeAlert: 280px;
