@import 'scss_header';

.manual-badge-wrap {
	text-align: left;

	.manual-badge, .auto-badge {
		display: inline-block;
		color: $colorBlack;
		margin-right: 1px;
		padding: 0 6px;
		background: #fff;
		text-transform: uppercase;
		font-size: 1.1rem;
		line-height: 1.8rem;
		box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.2);
	}

	.manual-badge {
		&.spot {
			color: $pointColor;
			font-style: italic;
			font-weight: 400;
		}
	}

	.auto-badge {
	}
}

.auto-badge-wrap {
	text-align: left;

	.auto-badge {
		display: inline-block;
		text-transform: uppercase;
		text-align: left;
		font-size: 0.9rem;
		line-height: 1.1rem;

		font-style: italic;

		+ .auto-badge {
			position: relative;
			margin-left: 4px;
			padding-left: 9px;

			&:before {
				content: '/';
				position: absolute;
				display: inline-block;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
			}
		}
	}
}

//s : 데이터 없는 경우
.no-data {
	width: 100%;
	padding: 80px 0;
	line-height: 1.46;

	.desc {
		font-size: $bodyText4;
		text-align: center;

		.spot {
			color: $pointColor;
		}
	}

	.btn-wrap {
		margin-top: 14px;
		text-align: center;

		.btn {
			min-width: 180px;
		}
	}

	&.has-line {
		border-top: 1px solid $colorBlack;
		border-bottom: 1px solid $colorLightestGray;
	}

	&.full {
		padding: 30px 0 0;
		height: calc(100vh - #{$headerHeightDefault} - 60px - 29px);
		@include justify_width(center);
		flex-direction: column;

		&.no-bottom-fix-type {
			height: calc(100vh - #{$headerHeightDefault} - 29px);
		}
	}
}

//e : 데이터 없는 경우

//s : 정보성 텍스트
.desc-gray, .desc-dark-gray {
	color: $colorLightGray !important;
	font-size: $bodyText6 !important;
	line-height: 1.6rem !important;

	.btn-link {
		margin-left: 5px;
		text-transform: initial;
	}

	.point {
		color: #f00;
	}
}

.desc-dark-gray {
	color: $colorDarkGray !important;;
}

.desc-black {
	font-size: $bodyText6;
	line-height: 1.6rem;
	color: $colorBlack !important;
}


.star-text, .line-text {
	position: relative;
	line-height: 1.6rem;

	&:before {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.star-text {
	padding-left: 8px;

	&:before {
		content: '*';
	}
}

.line-text {
	padding-left: 6px;

	&:before {
		content: '-';
	}
}

.text-list {
	margin-top: 12px;

	li {
		.btn-link {
			text-transform: initial;
		}

		+ li {
			margin-top: 6px;
		}
	}
}

.text-list-title {
	display: block;
	font-size: $bodyText5;
	margin-top: 15px;

	+ .text-list, + .line-text {
		margin-top: 12px;
	}
}

//e : 정보성 텍스트

//s : 프로필 공통
.profile-name {
	@include justify_width(start);
	vertical-align: top;
	align-items: center;

	.ratio-img-wrap {
		overflow: visible;
		flex-shrink: 0;
		-webkit-flex-shrink: 0;
		width: 36px;
		height: 36px;
		border: 1px solid $colorLightenGray !important;
		border-radius: 100%;
		font-family: $fontEng;
		font-size: $bodyText1;
		font-weight: 300;
		text-align: center;
		text-transform: uppercase;
		line-height: 3.4rem;
		margin-right: 8px;

		.img-viewer {
			overflow: hidden;
			width: calc(100% + 2px);
			height: calc(100% + 2px);
			margin: -1px;
			padding-top: 100% !important;
			border-radius: 100%;
		}
	}

	.profile-detail {
		overflow: hidden;
		margin-top: 3px;

		.fullname {
			font-size: $bodyText5;
			font-weight: 700;
			color: $colorBlack;
			@include ellipsis;
			display: block;
			width: auto;
			max-width: 100%;
			line-height: 1.17;
			word-break: break-all;
			vertical-align: top;
		}

		.profile-text {
			display: block;
			margin-top: 3px;
			font-size: 1rem;
			color: $colorLightGray;
			@include ellipsis;
			width: auto;
			max-width: 100%;
			line-height: 1.4;
		}

		.date {
			display: inline-block;
			font-size: 1rem;
			color: $colorLightGray;
			vertical-align: top;
		}

	}

	&.no-ellipsis {
		.ratio-img-wrap {
			align-self: flex-start;
		}

		.profile-detail {
			margin-top: 3px;

			.fullname, .profile-text {
				white-space: initial;
				text-overflow: initial;
			}
		}

		+ .profile-right {
			align-self: flex-start;

			.btn-link {
				margin-top: 14px;
			}
		}
	}

	&.profile-sm {
		.ratio-img-wrap {
			width: 24px;
			height: 24px;
			line-height: 2.2rem;
			font-size: $bodyText3;
			margin-right: 7px;
		}

		.profile-detail {
			margin-top: 0;
		}

		.fullname {
			font-size: $bodyText6;
			margin-right: 7px;
		}

		.date {
			margin-top: 2px;
		}
	}
}

.profile-flex-wrap {
	@include justify_width;
	align-items: center;

	.profile-name {
		position: relative;
		-webkit-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		overflow: hidden;
	}

	.profile-right {
		margin-left: 10px;

		.equal-value {
			display: inline-block;
			font-size: $bodyText5;
			vertical-align: top;
			margin: 13px 5px 0 15px;
		}

		button {
			~ button {
				margin-left: 2px;
			}

			&:last-child {
				margin-right: -3px;
			}
		}
	}
}

.my-profile-name {
	position: relative;
	display: inline-block;
	width: 60px;
	height: 60px;
	vertical-align: top;

	.ratio-img-wrap {
		overflow: visible;
		width: 56px;
		height: 56px;
		border: 1px solid $colorLightenGray;
		border-radius: 100%;
		font-family: $fontEng;
		font-size: 3rem;
		font-weight: 300;
		text-align: center;
		text-transform: uppercase;
		line-height: 5.4rem;

		.img-viewer {
			overflow: hidden;
			width: calc(100% + 2px);
			height: calc(100% + 2px);
			margin: -1px;
			padding-top: 100% !important;
			border-radius: 100%;
		}
	}

	.btn-profile-edit {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 34px;
		height: 34px;
		z-index: 2;

		&:before {
			content: '';
			display: block;
			width: 26px;
			height: 26px;
			margin: 4px;
			background-color: #fff;
			border-radius: 100%;
			box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
		}

		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 9px;
			left: 10px;
			width: 14px;
			height: 16px;
			@include background_img('../images/common/comm_profile_edit.png', 28px, 32px);
		}
	}
}

//e : 프로필 공통

// s: more text
.more-text-wrap {
	.text, .delete-text {
		font-size: 1.3rem;
		line-height: 2rem;
		letter-spacing: -0.033rem;
		color: $colorDarkGray;

	}

	.text {
		display: inline;
	}

	.delete-text {
		@include justify_width(start, true);
		vertical-align: 4px;
		margin-left: -2px;

		.tt-ellipsis {
			display: block;
			line-height: 1rem;
		}

		.tt-more {
			display: block;
			color: $pointColor;
			margin-left: 5px;
			padding-top: 1px;
			font-weight: 300;
			font-size: 1.2rem;
			line-height: 1.5rem;
		}
	}

	&.is-more {
		.delete-text {
			display: none;
		}
	}
}

// e: more text

//s : guide text
.guide-info-wrap {
	margin-top: 30px;
	text-align: center;

	.info-title {
		margin-bottom: 10px;
		font-size: $bodyText3;
		line-height: 1.8rem;

		.point {
			font-weight: 500;
		}
	}

	.info-desc {
		font-size: $bodyText5;
		font-weight: 300;
		line-height: 1.8rem;
		color: $colorBlack;
		word-break: keep-all;
		word-wrap: break-word;
	}

	.btn-wrap {
		margin-top: 14px;

		.btn {
			min-width: 180px;
		}
	}

	&:not(.no-bline) {
		+ .sctitle {
			padding-top: 40px;
			margin-top: 30px;
			border-top: 1px solid $colorLightenGray;
		}
	}
}

//e : guide text

//s : .complete-guide-wrap
.complete-guide-wrap {
	padding: {
		top: 40px;
	}
	text-align: center;

	.complete-title {
		font-size: 1.6rem;
		line-height: 2.4rem;

		+ .complete-stitle {
			margin-top: 10px;
			font-weight: 300;

			.complete-num {
				font-weight: 400;
			}
		}

	}

	.complete-stitle {
		font-size: $bodyText5;
		line-height: 1.8rem;

		+ .complete-desc {
			margin-top: 20px;
		}
	}

	.benefit-coupon-box {
		padding: 30px 0 50px;

		.desc-black {
			margin-top: 15px;
		}
	}

	.complete-desc {
		font-size: $bodyText5;
		line-height: 2rem;
		font-weight: 300;
	}

	.tbl-form {
		margin-top: 40px;
		padding: {
			top: 10px;
		}
		border-top: 1px solid $colorBlack;
		text-align: left;

		.form-box {
			.form-cont {
				color: $colorDarkGray;
			}

			&:last-of-type.horizontal {
				padding-bottom: 15px;
			}
		}

	}

	.btn-even-wrap {
		margin-top: 40px;
	}
}

//e : .complete-guide-wrap

// s: notice
.notice-desc {
	margin-top: 10px;
	position: relative;
	padding: 1px 0 1px 26px;

	&:before {
		content: 'notice';
		@include offscreen;
	}

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 20px;
		height: 20px;
		@include background_img('../images/common/comm_icon_notice.png', 40px, 40px);
	}

	font-size: 1.1rem;
	line-height: 1.8rem;

	&.is-hide {
		display: none;
	}
}

// e: notice