@import 'scss_header';

// s : 스와이퍼 스크롤바 공통
.swiper-container-horizontal {
	position: relative;
	overflow: hidden;

	> .swiper-scrollbar, > .swiper-pagination-progressbar {
		width: inherit;
		height: 2px;
		background: rgba(#fff, .9);
		border-radius: 0;
		left: 15px;
		right: 15px;
		bottom: 15px;
		z-index: 5;
	}

	.swiper-page-number {
		z-index: 2;
		position: absolute;
		top: 15px;
		right: 15px;

		> span {
			color: #fff;
			font-size: $bodyText4;
			line-height: 1.6rem;
			letter-spacing: 0.065rem;
		}
	}

	> .swiper-scrollbar {
		.swiper-scrollbar-drag {
			background: $colorBlack;
			border-radius: 0;
			transform-style: preserve-3d;
			-moz-transform-style: preserve-3d;
			-webkit-transform-style: preserve-3d;
		}
	}

	> .swiper-pagination-progressbar {
		top: inherit;

		.swiper-pagination-progressbar-fill {
			background: $colorBlack;
		}
	}
}

// e : 스와이퍼 스크롤바 공통

.btn-swiper {
	position: absolute;
	width: 30px;
	height: 100%;
	background: rgba(#fff, .7);
	top: 0;
	z-index: 3;

	.icon {
		display: inline-block;
		width: 15px;
		height: 15px;
		background: url(../images/common/comm_filter_btn_swiper_arrow.png) no-repeat 0 0;
	}

	&:hover, &:focus {
		background: #fff;
	}

	&.prev {
		left: 0;
	}

	&.next {
		right: 0;

		.icon {
			background-position: -15px 0;
		}
	}

	&.swiper-button-disabled {
		display: none;

		&.prev {
			.icon {
				background-position: 0 -15px;
			}
		}

		&.next {
			.icon {
				background-position: -15px -15px;
			}
		}
	}
}

// s : 스와이퍼 좌우 버튼 공통
.btn-swiper-arrow-wrap {
	font-size: 0;

	.btn-swiper-arrow {
		width: 40px;
		height: 40px;
		padding: 5px;
		background: #fff;
		border: {
			top: 1px solid $colorBlack;
			left: 1px solid $colorBlack;
		};


		.icon {
			display: inline-block;
			width: 30px;
			height: 30px;
			@include background_img('../images/ui/button/ui_btn_swiper_arrow.png', 120px, 60px);
		}

		&.prev {

		}

		&.next {
			.icon {
				background-position: -30px 0;
			}
		}

		&.swiper-button-disabled {
			pointer-events: none;

			.icon {
				opacity: .5;
			}
		}
	}


	/* s: 검색 영역 사용 */
	&.hsearch-type {
		position: absolute;
		top: -33px;
		right: 0;

		.btn-swiper-arrow {
			width: 10px;
			height: 20px;
			background-color: transparent;
			border: 0;

			.icon {
				width: 10px;
				height: 20px;
				background: url(../images/ui/button/comm_search_btn_arrow.png) no-repeat 0 0;
			}

			&.next {
				.icon {
					background-position: -10px 0;
				}
			}

		}
	}

	/* e: 검색 영역 사용 */

	// s : Small Type

	&.arrow-sm {
		.btn-swiper-arrow {
			width: 38px;
			height: 38px;

			.icon {
				width: 22px;
				height: 22px;
				background: url(../images/ui/button/ui_btn_swiper_arrow_sm.png) no-repeat 0 0;
			}

			&.prev {

			}

			&.next {
				.icon {
					background-position: -22px 0;
				}
			}

			&.swiper-button-disabled {
				pointer-events: none;

				.icon {
					opacity: .5;
				}
			}
		}
	}
}

// e : 스와이퍼 좌우 버튼 공통


// s : 상품 상세 이미지 스와이퍼
.prod-detail-wrap {
	.prod-detail-img-wrapper {
		&:before {
			display: block;
			width:100%;
			height:30px;
			opacity: 0.8;
			position: absolute;
			background:#fff;
			left:0;
			top:0;
			content:"";
			z-index:2;
		}
		&:after {
			display: block;
			width:100%;
			height:30px;
			line-height:30px;
			text-align:center;
			position: absolute;
			left:0;
			top:0;
			content:"회원 가입시 1만원 즉시 할인 무료배송 쿠폰 증정";
			font-size:1rem;
			z-index:3;
		}
	}	
}
.prod-detail-img-wrapper {
	position: relative;
	overflow: hidden;

	.prod-detail-img-wrap {

		.prod-detail-img-swiper {
			.prod-item {
				width: 83.3333%;
				padding: 0;
				border-right: 1px solid $colorBlack;
			}
		}
	}
}

// e : 상품 상세 이미지 스와이퍼


// s : 앵커 리스트 스와이퍼
.anchor-list-wrap {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	width: 30px;
	height: 100%;
	overflow: hidden;
	background: rgba(255, 255, 255, 0.6);
	z-index: 2;

	.anchor-list {
		padding: 6px 0;
		flex-direction: column;

		.anchor-item {
			height: initial;
			padding: 4px 0;
		}
	}

	.btn-anchor {
		display: block;
		width: 100%;
		font-size: $bodyText5;
		line-height: 2rem;
		text-align: center;
		color: $colorBlack;
		&:disabled, .disabled {
			opacity: 0.3;
		}
	}
}

// e : 앵커 리스트 스와이퍼

// s : 상품 리스트 스와이퍼
.prd-list-swiper-wrap {
	margin-left: -30px;
	padding: 0 30px 0 15px;

	.prod-item {
		/*width: 150px;*/
		padding-left: 15px;
		transform: translateX(15px);

	}
}

// e : 상품 리스트 스와이퍼

// s : gate-banner-wrap
.gate-banner-wrap {
	position: relative;
	margin-top: 40px;

	.gate-banner-swiper {
		border-top: 1px solid $colorBlack;

		.swiper-slide {
			.gate-view-item {
				@include justify_width;
				height: 140px;
				padding: 15px 15px;
				border-bottom: 1px solid $colorBlack;

				.ratio-img-wrap {
					width: 165px;

					.img-viewer {
						padding-top: 66.6666%;
					}
				}

				.view-info-wrap {
					flex: 1;
					margin-left: 10px;

					.view-title-box {
						.title {
							margin-top: -2px;
							font-size: $bodyText3;
							font-weight: 500;
							letter-spacing: -0.035rem;
							@include multi_ellipsis(2rem);
							color: $colorBlack;
						}

						.desc {
							margin-top: 5px;
							font-size: $bodyText4;
							letter-spacing: -0.033rem;
							@include multi_ellipsis(2rem);
							color: $colorDarkGray;
						}
					}

					.gatemenu-type {
						display: block;
						margin-top: 13px;
						font-size: $bodyText5;
						letter-spacing: 0.024rem;
						line-height: 1.5rem;
						color: $colorBlack;
					}
				}
			}
		}

	}

	.btn-swiper-arrow-wrap {
		display: none;
		text-align: right;
	}

	&.is-active {
		.btn-swiper-arrow-wrap {
			display: block;
		}
	}
}

// e : gate-banner-wrap

// s : 포토 리뷰 팝업 스와이퍼
.photo-review-thumb-wrap {

	.swiper-wrapper {
		.swiper-slide {

			.prod-review-img-thumb {
				position: relative;
				display: inline-block;
				width: 100%;
				font-size: 0;

				.review-img-thumb {
					width: 100%;
					height: auto;
					padding-top: 100%;
				}

				&.selected {
					&:after {
						content: '';
						position: absolute;
						width: 100%;
						left: 0;
						top: 0;
						bottom: 0;
						z-index: 1;
						border: 1px solid $colorBlack;
					}
				}
			}
		}
	}
}

.photo-review-content-wrap {
	margin: 21px 14px 20px;
}

// e : 포토 리뷰 팝업 스와이퍼