@import 'scss_header';

// s : jQuery UI - Tab
.ui-tabs {
	padding: 0;

	.ui-tabs-nav {
		padding: 0;

		li {
			margin-top: 0;

			&.ui-tabs-active {
				margin-bottom: 0;
				padding-bottom: 0;
			}
		}

		.ui-tabs-anchor {
			width: 100%;
			padding: 0 21px;

			.tab-cnt {
				display: inline-block;
				margin-left: 4px;
			}
		}
	}

	.ui-tabs-panel {
		padding: 0;
	}
}

.wrap {
	.contents-wrap {
		.tab-wrap {
			&:first-child {
				.tabs {
					border-top: none;
				}
			}
		}
	}
}

.tab-wrap {
	overflow: hidden;

	.tabs {
		height: 40px;
		border: {
			top: 1px solid $colorBlack;
			bottom: 1px solid $colorLightenGray;
		}

		.tab-item {
			background-color: #fff;
			vertical-align: top;
			text-align: center;
			-webkit-box-flex: 1;
			-ms-flex: 1;
			flex: 1;

			~ li {
				border-left: 1px solid $colorLightenGray;
			}

			.tab-link, .ui-tabs-anchor {
				@include ellipsis;
				overflow: hidden;
				height: 39px;
				padding: 0 5px;
				font-size: $bodyText5;
				font-weight: 300;
				line-height: 3.9rem;
				text-align: center;
				color: $colorLightGray;
				text-transform: uppercase;

				&.tab-disabled {
					color: lighten($colorBlack, 80%) !important;
				}
			}

			&.ui-tabs-active {

				.tab-link, .ui-tabs-anchor {
					font-weight: 400;
					color: $colorBlack;
					pointer-events: none;
				}
			}

			&.ui-state-disabled {
				&:hover {
					cursor: not-allowed;
				}
			}

			&.is-active {
				.tab-link, .ui-tabs-anchor {
					font-weight: 400;
					color: $colorBlack;
					pointer-events: none;
				}
			}
		}
	}

	.tab-content {
		.no-data {
			&.full {
				height: calc(100vh - #{$headerHeightDefault} - 60px - 29px - 40px);
			}
		}
	}

	&:not([class*=swiper-]) {
		.tabs {
			@include justify_width(start);
		}
	}

	.swiper-tab-box {
		background: #fff;
		border: {
			bottom: 1px solid $colorLightestGray;
		}
		padding: 0 15px;
		margin: 0 -8px;

		&.sps-blw {
			z-index: 5;
			position: fixed;
			left: 0;
			top: 0;
			right: 0;
			bottom: inherit;
			@at-root .wrap .header-wrap ~ .contents-wrap & {
				top: 45px;
			}
			~ .tab-content {
				padding-top: 40px;
			}
		}

		&.no-line {
			border-bottom: none;
		}

		.tabs {
			height: 40px;
			border: 0;

			.tab-item {
				flex: 0 1 auto;
				display: inline-block;
				width: auto;
				padding: 6px 0 5px;
				vertical-align: top;

				.tab-link, .ui-tabs-anchor {
					padding: 0 7px;
					height: 2.8rem;
					display: block;
					line-height: 2.8rem;
					color: $colorGray;
					outline: none;
				}

				~ li {
					border-left: 0;
				}

				/*&.ui-tabs-active {
					pointer-events: none;

					.tab-link, .ui-tabs-anchor {
						font-weight: 400;
						color: $colorBlack;
						transition: color 0.5s;
						pointer-events: none;
					}
				}*/

				&.is-active {
					pointer-events: none;

					.tab-link, .ui-tabs-anchor {
						font-weight: 400;
						color: $colorBlack;
						transition: color 0.5s;
						pointer-events: none;
					}
				}

				&.ui-state-disabled {
					&:hover {
						cursor: not-allowed;
					}
				}

			}
		}
	}

	&.bottom-line {
		margin-top: 25px;

		.tabs {
			height: 34px;
			border: {
				top: none;
				bottom: none;
			}

			.tab-item {
				border-bottom: 1px solid $colorLightestGray;

				&.ui-tabs-active {
					border-bottom-color: $colorBlack;
				}

				.tab-link, .ui-tabs-anchor {
					height: 33px;
					line-height: 3.3rem;
				}

				~ li {
					border-left: none;
				}
			}
		}

		+ .text-list-title {
			margin-top: 24px;
		}
	}
}

// e : jQuery UI - Tab